import React, { useEffect, useState } from 'react';
import {
	BasicTextField,
	BasicSelect,
} from 'Components/UI/';
import { clientInformationStyles } from '../../ClientProfilePage'
import { makeStyles } from '@material-ui/core/styles';
import { regionList,cityList } from 'Pages/CreateProposal/Details';
import { 
	faPlus, faTimesCircle, faMinusCircle,
	//faTimesCircle,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	Grid,
	Typography,
	//OutlinedInput,
	//InputAdornment,
	InputLabel,
	//Chip,
} from '@material-ui/core';
import Link from '@material-ui/core/Link';
import {
	GridHorizontalDivider
} from 'Components/UI';
import ChipInput from 'material-ui-chip-input';

interface ClientInformation {
	invalidFields: string[];
	clientInfo?:any;
	setEditInfo?:(e) => void;
	setData:any;
	setSideNav?:(e)=>void;
}

interface ClientContactAddress { // Just for intellisense
	branch_name: string;
    floor: number;
    unit: string;
    bldg_no: string;
    bldg_name: string;
    street: string;
    brgy: string;
    city: string;
    province?: string;
    region: string;
    zip_code: string;
    contact_numbers: { no: string }[];
    contact_persons: ContactPerson[];
}

interface ContactPerson {
	name: string;
	contact_no: string;
	email_address: string;
	designation: string;
}

const insstyle = makeStyles(theme => ({
	textField: {
			marginTop: '10px',
		marginBottom: '20px',
		'& label.Mui-focused': {
		  color: 'rgb(21,28,28)',
		},
		'& .MuiInput-underline:after': {
		  borderBottomColor: 'rgba(39,52,76,0.3)',
		},
		'& .MuiOutlinedInput-root': {
		  '& fieldset': {
			borderColor: 'rgba(39,52,76,0.3)',
			borderRadius: 5,
		  },
		  '&:hover fieldset': {
			borderColor: 'rgba(39,52,76,0.3)',
			borderRadius: 5,
		  },
		  '&.Mui-focused fieldset': {
  			borderRadius: 5,
			borderColor: 'rgba(39,52,76,0.5)',
		  },
		},
		'&': {
		  marginTop: theme.spacing(1),
		  backgroundColor: 'white',
		  width: '100%',
		},
	  },
}))

export const EditContactDetails = (props:ClientInformation) => {

	const classes = clientInformationStyles();
	const selfclass = insstyle();
	const [initialized, setInitialize] = useState(false);
	const [clientInfo, setClientInfo] = useState<any>();

	console.log('edit', clientInfo ? clientInfo: '');

	useEffect(() => {
		if (!initialized) {
			setClientInfo(props.clientInfo);
			setInitialize(true);
			console.log(clientInfo);
		}
	});

	filteredCityList['city'] = cityList.filter(element => 
		element['region'] == props.clientInfo.main_office_address.region
	);

	const changeValueOnBranch = (address: ClientContactAddress, keyName: string) => (event: any) => {
		address[keyName] = event.target.value;
		setClientInfo({...clientInfo});
	};

	const changeValueOnBranchAsChip = (address: ClientContactAddress, keyName: string) => (tels: string[]) => {
		address[keyName] = tels.map(tel => { return { no: tel }});
		setClientInfo({...clientInfo});
	};

	const changeContactDetail = (contact: ContactPerson, keyName: string) => (event: any) => {
		contact[keyName] = event.target.value;
		setClientInfo({...clientInfo});
	}

	const addEmptyBranch = () => {
		const newBranch: ClientContactAddress = {
			branch_name: '',
			floor: 0,
			unit: '',
			bldg_no: '',
			bldg_name: '',
			street: '',
			brgy: '',
			city: '',
			province: '',
			region: '',
			zip_code: '',
			contact_numbers: [],
			contact_persons: []
		}
		clientInfo.branches.push(newBranch);
		setClientInfo({...clientInfo});
	}

	const removeBranch = (branchIndex: number) => {
		clientInfo.branches.splice(branchIndex, 1);
		setClientInfo({...clientInfo});
	}

	const addEmptyContactPerson = (address: ClientContactAddress) => {

		address.contact_persons.push({
			name: '',
			contact_no: '',
			email_address: '',
			designation: ''
		});
		setClientInfo({...clientInfo});
	}

	const removeContactPerson = (address: ClientContactAddress, contactIndex: number) => {
		address.contact_persons.splice(contactIndex, 1);
		setClientInfo({...clientInfo});
	}


	let telno:string[] = [];
	for(let x=0;x<props.clientInfo.main_office_address.contact_numbers.length;x++) {
		if(props.clientInfo.main_office_address.contact_numbers[x]['no']) {
			telno.push(props.clientInfo.main_office_address.contact_numbers[x]['no'].toString());
		}
	}

	let contname:string[] = [];
	for(let x=0;x<props.clientInfo.main_office_address.contact_numbers.length;x++) {
		if(props.clientInfo.main_office_address.contact_persons[x]) {
			contname.push(props.clientInfo.main_office_address.contact_persons[x]['name'].toString());
		}
	}

	const renderBranch = (address: ClientContactAddress, isMainBranch: boolean, onRemove?: Function) => {
		return (
			<Grid container>
				{!isMainBranch ? (
					<>
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="branchName"
							label="Branch Name*"
							value={address.branch_name}
							onChange={changeValueOnBranch(address, 'branch_name')} />
					</Grid>
					<Grid item xs={6} className={classes.editContainer} style={{paddingTop: '2rem'}}>
						<Typography className={classes.contactheader}>
							<Link 
								className={classes.linkStyleDisabled}
								onClick={() => onRemove ? onRemove() : null}>
								<FontAwesomeIcon 
									icon={faMinusCircle}
									className={classes.downloadDisabled}
								/>Delete this branch
							</Link>
						</Typography>
					</Grid>
					</>
				) : null}
				<Grid item xs={1} className={classes.editContainer}>
					<BasicTextField
						id="floor"
						label="Floor*"
						value={address.floor}
						onChange={changeValueOnBranch(address, 'floor')} />
				</Grid>
				<Grid item xs={1}  className={classes.editContainer}>
					<BasicTextField 
						id="unit"
						label="Unit"
						value={address.unit}
						onChange={changeValueOnBranch(address, 'unit')} />
				</Grid>
				<Grid item xs={4} className={classes.editContainer}>
					<BasicTextField
						id="buildingNumber"
						label="Building Number*"
						value={address.bldg_no}
						onChange={changeValueOnBranch(address, 'bldg_no')} />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicTextField
						id="buildingName"
						label="Building Name*"
						value={address.bldg_name}
						onChange={changeValueOnBranch(address, 'bldg_name')} />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicTextField
						id="street"
						label="Street*"
						value={address.street}
						onChange={changeValueOnBranch(address, 'street')} />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicTextField
						id="barangay"
						label="Barangay*"
						value={address.brgy}
						onChange={changeValueOnBranch(address, 'brgy')} />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicSelect
						id="region"
						label="Region*"
						menuItems={regionList}
						value={address.region}
						onChange={changeValueOnBranch(address, 'region')}
						width='100%' />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicSelect
						id="province"
						label="Province*"
						menuItems={regionList}
						value={address.province}
						onChange={changeValueOnBranch(address, 'province')}
						width='100%' />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicSelect
						id="city"
						label="Municipality/City*"
						menuItems={filteredCityList['city']}
						value={address.city}
						onChange={changeValueOnBranch(address, 'city')}
						width='100%' />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<BasicTextField
						id="zipCode"
						label="Zip Code*"
						value={address.zip_code}
						onChange={changeValueOnBranch(address, 'zip_code')} />
				</Grid>
				<Grid item xs={6} className={classes.editContainer}>
					<InputLabel className={classes.inputLabel}>
						Telephone Number/Trunk Line*
					</InputLabel>
					<ChipInput
						defaultValue={address.contact_numbers.map(contact => { return contact.no; })}
						className={selfclass.textField}
						onChange={changeValueOnBranchAsChip(address, 'contact_numbers')}
						variant='outlined' />
				</Grid>
				<GridHorizontalDivider height="30px" />
				<Grid item xs={12} className={classes.editContainer}>
					<div>
						<Grid container>
							{address.contact_persons && address.contact_persons.length > 0 ? (
								<Grid item xs={6}  className={classes.editContainer}>
									<Typography className={classes.contactHeader}>
										Contact Person/s
									</Typography>
								</Grid>
							) : null}
							<Grid item xs={6} direction='row-reverse' className={classes.editContainer}>
								<Typography className={classes.contactheader}>
									<Link 
										className={classes.linkStyleSelected}
										onClick={() => {addEmptyContactPerson(address)}}>
										<FontAwesomeIcon 
											icon={faPlus}
											className={classes.downloadIcon}
										/>Add Contact Person
									</Link>
								</Typography>
							</Grid>
						</Grid>
						<GridHorizontalDivider height="30px" />
					</div>
					{address.contact_persons && address.contact_persons.length > 0 ? (
						<>
						<div>
							<Grid container>
								<Grid item xs={3} className={classes.editContainer}>Names</Grid>
								<Grid item xs={3} className={classes.editContainer}>Position</Grid>
								<Grid item xs={3} className={classes.editContainer}>Contact Number</Grid>
								<Grid item xs={3} className={classes.editContainer}>Email Address</Grid>
							</Grid>
						</div>	
						<div>
							<Grid container>
								{address.contact_persons.map((contact, index) => {
									return renderContactPerson(contact, 
										() => {removeContactPerson(address, index)});
								})}
							</Grid>
						</div>
						</>
					) : null}
				</Grid>
			</Grid>
		);
	}

	const renderContactPerson = (contact: ContactPerson, onRemove: Function) => {
		return (
			<>
				<Grid item xs={3} className={classes.editContainer}>
					<BasicTextField
						id="contactName"
						value={contact.name}
						onChange={changeContactDetail(contact, 'name')} />
				</Grid>
				<Grid item xs={3} className={classes.editContainer}>
					<BasicTextField
						id="contactPosition"
						value={contact.designation}
						onChange={changeContactDetail(contact, 'designation')} />
				</Grid>
				<Grid item xs={3} className={classes.editContainer}>
					<BasicTextField
						id="contactNo"
						value={contact.contact_no}
						onChange={changeContactDetail(contact, 'contact_no')} />
				</Grid>
				<Grid item xs={3} className={classes.editContainer}>
					<div style={{position: 'relative'}}>
						<div style={{width: '85%'}}>
							<BasicTextField
								id="contactEmail"
								value={contact.email_address}
								onChange={changeContactDetail(contact, 'email_address')} />
						</div>
						<div 
							style={{position: 'absolute', top: 18, right: 0}}
							onClick={() => onRemove()}>
							<FontAwesomeIcon
								icon={faTimesCircle}
								className={classes.searchInputAdornment} />
						</div>
					</div>
				</Grid>
			</>
		);
	}

	return (
		<Grid container>
			<Grid item xs={6}  className={classes.editContainer}>
				<Typography className={classes.contactHeader}>
					MAIN OFFICE ADDRESS
				</Typography>
			</Grid>
			<Grid item xs={6} direction='row-reverse' className={classes.editContainer}>
				<Typography className={classes.contactheader}>
					<Link 
						className={classes.linkStyleSelected}
						onClick={addEmptyBranch}>
						<FontAwesomeIcon 
							icon={faPlus}
							className={classes.downloadIcon}
						/>Add Branch
					</Link>
				</Typography>
			</Grid>
			<GridHorizontalDivider height="30px" />
			{clientInfo ? renderBranch(clientInfo.main_office_address, true) : null}
			{clientInfo ? (
				<div>
					{clientInfo.branches.map((branch, index) => {
						return (
							<div id={`branch_${index}`}>
								<GridHorizontalDivider height="50px" />
								<hr />
								<GridHorizontalDivider height="50px" />
								<Typography className={classes.contactHeader}>
									{`BRANCH ${index + 2}`}
								</Typography>
								<GridHorizontalDivider height="30px" />
								{renderBranch(branch, false, () => {removeBranch(index)})}
							</div>
						)
					})}
				</div>
			) : null}
			<GridHorizontalDivider height="100px" />
		</Grid>
		
	);
};



let filteredCityList = [
	{'city':[{name:'',value:'',region:''}]},
];
