

import React from 'react';
import {
	Grid,
} from '@material-ui/core';
import {
	BasicTextField,
	BasicSelect,
} from 'Components/UI/';
import { clientInformationStyles } from '../../ClientProfilePage'

import { industryList,ownershipList } from 'Pages/CreateProposal/Details';
interface ClientInformation {
	invalidFields: string[];
	clientInfo?:any;
	setEditInfo?:(e) => void;
	setData:any;
	setSideNav?:(e)=>void;
}

export const EditNature = (props:ClientInformation) => {
	const classes = clientInformationStyles();
	const handleTextFieldOnChange = (name: string) => (event: any) => {
		const pattern = new RegExp(/\D/);
		let value: any;
		event.target.value = event.target.value.trim();
		if (event.target.type === 'number' && event.target.value !== '') {
			value = event.target.valueAsNumber;
		} else {
			value = event.target.value;
		}
		if(name === "years_of_existence" && pattern.test(value)){
			const data = { ...props.clientInfo };
			console.log('date '+JSON.stringify(data,null,2))
			if (props.setData) {
				props.setData(data);
			}
		}else{
			const data = { ...props.clientInfo, [name]: value };
			if (props.setData) {
				props.setData(data);
			}
		}
		
	};
	return (
		<Grid container>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicSelect
						id="industry"
						label="Industry*"
						menuItems={industryList}
						value={props.clientInfo.industry}
						onChange={handleTextFieldOnChange('industry')}
						width='100%'
					/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicSelect
						id="ownership"
						label="Ownership*"
						menuItems={ownershipList}
						value={props.clientInfo.ownership}
						onChange={handleTextFieldOnChange('ownership')}
						width='100%'
					/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
						id="yearsOfExistence"
						type='tel'
						label="Years of Existence"
						value={props.clientInfo.years_of_existence}
						onChange={handleTextFieldOnChange('years_of_existence')}
					/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
						id="sourcesOfFunds"
						label="Sources of Funds"
						value={props.clientInfo.sources_of_funds}
						onChange={handleTextFieldOnChange('sources_of_funds')}
					/>
			</Grid>
		</Grid>
		);
};
