import * as React from 'react';
//import { useState } from 'react';
import { 
    Grid, 
    Typography,
} from '@material-ui/core/';
import { 
    makeStyles,
} from '@material-ui/core/styles';
import { Components } from '@hims/core';

const InputTextField = Components.UI.InputTextField.InputTextField;

const sifielditemStyles = makeStyles({
    disabledsiname: {
      fontSize: '1rem',
      color: 'rgba(39, 46, 76, 0.4)',
      paddingBottom: 4,
    },
    enabledsiname: {
        fontSize: '1rem',
        color: 'rgba(39, 46, 76, 1)',
        paddingBottom: 4,
    },
});

interface SIFieldItemProps {
    label?: string;
    value: string;
    disabled?: boolean;
    handleChange?: any;
    required?: boolean;
}

export const SITextField: React.FC<SIFieldItemProps> = (
	props: SIFieldItemProps,
): JSX.Element => {

    const classes = sifielditemStyles(props);

    let error = props.required && (props.value === null || props.value.trim().length < 1);
    let errorMessage = (props.required && (props.value === null || props.value.trim().length < 1)) ? props.label+' is required' : ''; 
    
    return(
        <Grid container alignItems="flex-start" style={{paddingTop: 12, paddingBottom: 12, paddingRight: 24}}>
            <Grid item xs={12}>
                <Typography className={props.disabled ? classes.disabledsiname : classes.enabledsiname}>{props.label}</Typography>
            </Grid>
            <Grid item xs={12}>
                <InputTextField
                    id={props.label ? props.label.toLowerCase().replace(' ', '-') : ''}
                    disabled={props.disabled}
                    onChange={props.handleChange}
                    error={error}
                    errorMessage={errorMessage}
                    value={props.value} />
            </Grid>
        </Grid>
    );
}

SITextField.defaultProps = {
    disabled: false,
    label: '',
    value: '',
    required: false,
    handleChange: function(){}
}