import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Typography } from '@material-ui/core/';
import clsx from 'clsx';
import { SmallSICheckboxItem } from '../SmallSICheckboxItem';
import { pull } from 'lodash';

interface PrintingOfCardsProps {
    data: any;
    updateData: any;
}

const itemStyles = makeStyles({
    reminder: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
});

export const PrintingOfCards: React.FC<PrintingOfCardsProps> = (
    props: PrintingOfCardsProps,
): JSX.Element => {

    const classes = itemStyles(props);

    const handleCheckboxChange = (code: string, newValue: boolean) => {
        if(newValue){
            if(props.data){
                props.updateData('printing_of_cards', [...props.data, code]);
            }
            else{
                props.updateData('printing_of_cards', [code]);
            }
        }
        else{
            if(props.data){
                props.updateData('printing_of_cards', [...pull(props.data, code)]);
            }
            else{
                props.updateData('printing_of_cards', []);
            }
        }
    };

    const isChecked = (code: string) => {
        return props.data && props.data.indexOf(code) > -1;
    };

    return (
        <div>
            <Grid container>
                <Grid item xs={12} md={6} style={{marginRight: 16}}>
                    
                    <Typography className={classes.reminder}>
                    Print in the card only the items that are checked below. Use the sample on the right to see layout of the card ID.
                    </Typography>
                    <SmallSICheckboxItem 
                        label="1. Company Logo"
                        checked={isChecked('company logo')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('company logo', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="2. ID Picture"
                        checked={isChecked('id picture')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('id picture', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="3. Member Last Name, First Name, Middle Name, Extension (if any)"
                        checked={isChecked('full name')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('full name', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="4. Company Name"
                        checked={isChecked('company name')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('company name', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="5. Member Account No."
                        checked={isChecked('member account no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('member account no', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="6. Card Number"
                        checked={isChecked('card no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('card no', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="7. Month and Year of Birth (MM/YY)"
                        checked={isChecked('birthday')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('birthday', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="8. Sex"
                        checked={isChecked('sex')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('sex', newValue);
                        }} />
                    <SmallSICheckboxItem 
                        label="9. Dental Coverage — Based on the member’s plan type in the contract"
                        checked={isChecked('dental coverage')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('dental coverage', newValue);
                        }} />
                </Grid>
                <Grid item xs={12} md={5} >
                    <img
                        src={'si_sample_card.png'}
                        className={clsx('si-printing-of-card-sample-img')}
                        alt="Sample Reference Card" />
                </Grid>
            </Grid>
        </div>
    );
};