    const getInvalidFields = (nav:string): any => {
      let fields: string[] = [];

      if(uploadedFile.length === 0){
        fields.push('Uploaded File');
      }

      switch (nav){
        case 'signed_contracts':
          if(docname === ''){
            fields.push('Document Name');
          }
          if(fileversion === ''){
            fields.push('Version');
          }
          if(!datesignedFieldCorrect){
            fields.push('Date Signed');
          }
          break;
        case 'signed_proposals':
          if(proposalno === ''){
            fields.push('Proposal Number');
          }
          if(fileversion === ''){
            fields.push('Version');
          }
          if(!datesignedFieldCorrect){
            fields.push('Date Signed');
          }
          break;
        case 'masterlists':
          if(docname === ''){
            fields.push('Document Name');
          }
          if(batchname === ''){
            fields.push('Batch Name');
          }
          break;
        case 'apd_endorsements':
          if(docname === ''){
            fields.push('Document Name');
          }
          if(apdtype === ''){
            fields.push('APD Type');
          }
          if(!datesignedFieldCorrect){
            fields.push('Date Endorsed');
          }
          break;
        case 'corporate_guarantees':
        case 'letters_and_requests':
          if(docname === ''){
            fields.push('Document Name');
          }
          if(corpsender === ''){
            fields.push('Sender');
          }
          break;
        default:
          break;
      }
      setInvalidFields(fields);
      return {fields:fields}
    }
    

