import { createSelector } from 'reselect';

const getClientInfo = (state: any) => {
    return state.pages.clientProfile.clientInfo;
  };

  const getProposalInfo = (state: any) => {
    return state.pages.clientProfile.proposalInfo;
  }

  const selectBusinessData = createSelector(getClientInfo, (ClientInfo) => {
    return ClientInfo.businessData;
  });
  
  const selectNatureData = createSelector(getClientInfo, (ClientInfo) => {
    return ClientInfo.natureData;
  });
  
  const selectContactDetailsData = createSelector(getClientInfo, (ClientInfo) => {
    return ClientInfo.contactDetailsData;
  });
  
  const selectHMOData = createSelector(getClientInfo, (ClientInfo) => {
    return ClientInfo.HMOData;
  });
  export {
    getClientInfo,
    getProposalInfo,
    selectBusinessData,
    selectNatureData,
    selectContactDetailsData,
    selectHMOData
  }