import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { assign, cloneDeep, forEach, snakeCase, toLower } from 'lodash';

import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid, Tabs, Tab } from '@material-ui/core';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import { TableComponent } from 'Components/UI/TableComponent';
import { FloatingButtons } from 'Components/UI/FloatingButtons';

import { AddMemberFieldDrawer } from './AddMemberFieldDrawer';

const memberFieldsPageStyles = makeStyles(() => ({
  main: {
    backgroundColor: '#F5F7FB',
    padding: '32px 24px',
  },
  addFieldIcon: {
    color: '#0D5E40',
    cursor: 'pointer',
    fontSize: '13px',
    marginRight: '8px',
  },
}));

interface MemberFieldsPageProps {
  clientData: any;
  hasChanges: boolean;
  onClientDataUpdated: (clientData: any) => void;
  openMFModal: (title: string, message: string) => void;
  openConfirmationModal: (mfData: any) => void;
  setHasChanges: (hasChanges: boolean) => void;
}

export const MemberFieldsPage: React.FC<MemberFieldsPageProps> = (
  props: MemberFieldsPageProps,
): JSX.Element => {
  const classes = memberFieldsPageStyles();

  const defaultTableData: any = {
    formattedColumns: {
      required_checkbox: ['is_required'],
    },
    columnExtensions: [],
    columns: [
      {
        name: 'is_required',
        title: 'Required?',
      },
      {
        name: 'system_name',
        title: 'Field',
      },
      {
        name: 'input_type',
        title: 'Input Type',
      },
      {
        name: 'data_type',
        title: 'Data Type',
      },
    ],
    rows: [],
  };

  const [limit] = useState(5);
  const [tabs] = useState(['Client-required', 'Mandatory']);
  const [selectedTab, setSelectedTab] = useState(0);
  const [memberFieldsData, setMemberFieldsData] = useState<any>([]);
  const [clientRequiredFields, setClientRequiredFields] = useState(
    defaultTableData,
  );
  const [mandatoryFields, setMandatoryFields] = useState(defaultTableData);
  const [clientAddedFields, setClientAddedFields] = useState(0);
  const [lastSortOrder, setLastSortOrder] = useState(0);

  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  /**
   * Get member fields from API upon page load.
   */
  useEffect(() => {
    const { member_data_fields } = props.clientData;
    if (member_data_fields) {
      setMemberFieldsData(member_data_fields);
    }
  }, [props.clientData]);

  /**
   * Update tables data when memberFieldsData updates.
   */
  useEffect(() => {
    parseMemberFields(memberFieldsData);
  }, [memberFieldsData]);

  const getNextSortOrder = () => {
    const nextSortOrder = lastSortOrder + 1;
    setLastSortOrder(nextSortOrder);

    return nextSortOrder;
  };

  const handleTabChange = (
    _event: React.ChangeEvent<HTMLDivElement>,
    tab: number,
  ) => {
    setSelectedTab(tab);
  };

  const handleIsRequiredClick = (rowData: any) => {
    const {
      data: { row: changedData },
    } = rowData;

    if (changedData && changedData.field_type !== 'Mandatory') {
      const memberFieldsDataCopy = cloneDeep(memberFieldsData);
      forEach(memberFieldsDataCopy, (memberField, idx) => {
        if (
          memberField.system_name === changedData.system_name &&
          memberField.field_name === changedData.field_name
        ) {
          memberField.is_required = !memberField.is_required;
          memberFieldsDataCopy[idx] = memberField;
        }
      });

      setMemberFieldsData(memberFieldsDataCopy);
      props.setHasChanges(true);
    }
  };

  const onAddDataFieldBtnClicked = () => {
    if (clientAddedFields >= limit) {
      props.openMFModal(
        'Maximum limit reached',
        'Maximum number of custom data fields has been reached.'
      );

      return;
    }

    setIsDrawerOpen(true);
  };

  const parseMemberFields = memberFields => {
    if (memberFields && memberFields.length > 0) {
      let numAddedFields = 0;
      let sortOrder = 0;

      const clientFieldData = assign({}, defaultTableData);
      const clientFieldRows: any[] = [];

      const mandatoryFieldData = assign({}, defaultTableData);
      const mandatoryFieldRows: any[] = [];

      forEach(memberFields, memberField => {
        if (toLower(memberField.field_type) === toLower('Client-required')) {
          clientFieldRows.push(memberField);
        } else if (toLower(memberField.field_type) === toLower('Mandatory')) {
          mandatoryFieldRows.push(memberField);
        }

        if (memberField.is_client_added) {
          numAddedFields++;
        }

        const { sort_order: fieldSortOrder } = memberField;
        if (fieldSortOrder && fieldSortOrder > sortOrder) {
          sortOrder = fieldSortOrder;
        }
      });
      // Set number of added fields
      setClientAddedFields(numAddedFields);

      // Set sort order
      setLastSortOrder(sortOrder);

      // Client-required fields table data
      clientFieldData.rows = clientFieldRows;
      setClientRequiredFields(clientFieldData);

      // Mandatory fields table data
      mandatoryFieldData.rows = mandatoryFieldRows;
      setMandatoryFields(mandatoryFieldData);
    }
  };

  const onDrawerClose = () => {
    setIsDrawerOpen(false);
  };

  const onDrawerSubmit = (data: any) => {
    if (!isValidMemberFieldData(data)) {
      return;
    }

    addToMemberFieldsData(data);
    setIsDrawerOpen(false);
    props.setHasChanges(true);
  };

  const isValidMemberFieldData = (data: any) => {
    const {
      fieldName,
      fieldInputType,
      fieldDataType,
      fieldDropdownValues,
    } = data;

    if (fieldName === '') {
      props.openMFModal(
        'Empty field name',
        'Field name cannot be empty.'
      );

      return false;
    }

    if (fieldInputType === '') {
      props.openMFModal(
        'Input type not selected',
        'Please select an input type.'
      );

      return false;
    }

    if (fieldInputType === 'Text Field' && fieldDataType === '') {
      props.openMFModal(
        'Data type not selected',
        'Please select a data type.'
      );

      return false;
    }

    if (fieldInputType === 'Dropdown' && fieldDropdownValues.length === 0) {
      props.openMFModal(
        'No dropdown options',
        'Please provide at least one dropdown option.'
      );

      return false;
    }

    return true;
  };

  const addToMemberFieldsData = (data: any) => {
    const newMemberField = {
      system_name: data.fieldName,
      field_name: snakeCase(data.fieldName),
      is_required: data.fieldIsRequired,
      input_type: data.fieldInputType,
      data_type: data.fieldDataType,
      info_type: 'Member Profile',
      field_type: 'Client-required',
      field_choices: data.fieldDropdownValues,
      is_client_added: true,
      sort_order: getNextSortOrder(),
    };

    setMemberFieldsData([...memberFieldsData, newMemberField]);
  };

  const handleSaveChanges = () => {
    const { _id: clientId } = props.clientData;
    if (clientId) {
      const newClientData = cloneDeep(props.clientData);
      newClientData.member_data_fields = memberFieldsData;

      props.openConfirmationModal(newClientData);
    }
  }

  return (
    <div className={classes.main}>
      <Grid container>
        <Grid item xs={8}>
          <Tabs
            value={selectedTab}
            onChange={handleTabChange}
            aria-label="simple tabs example"
          >
            {tabs.map((tab, idx) => (
              <Tab
                id={'verify-member-tab-' + idx}
                label={tab}
                key={'verify-member-tab-' + idx}
                aria-controls={'verify-member-tabpanel-' + idx}
              />
            ))}
          </Tabs>
        </Grid>
        <Grid item xs={4} style={{ paddingTop: '8px' }}>
          <Grid container justify="flex-end" alignItems="flex-end">
            <Button
              onClick={onAddDataFieldBtnClicked}
              variant="text"
              color="primary"
            >
              <FontAwesomeIcon icon={faPlus} className={classes.addFieldIcon} />
              Add a Custom Data Field
            </Button>
          </Grid>
        </Grid>
        <Grid item xs={12} style={{ paddingTop: '8px' }}>
          {selectedTab === 0 ? (
            <TableComponent
              id="tab-partial"
              rows={
                Object.keys(clientRequiredFields).length > 0
                  ? clientRequiredFields['rows']
                  : []
              }
              columns={
                Object.keys(clientRequiredFields).length > 0
                  ? clientRequiredFields['columns']
                  : []
              }
              message="No available data"
              showSelectionColumn={false}
              onClickRow={(_row: number[]) => {}}
              onClickCell={handleIsRequiredClick}
              disableSelect
              disableSearch
              disableFilter
              disableSort
              disablePaging
              singleSelect={true}
              formattedColumns={
                Object.keys(clientRequiredFields).length > 0
                  ? clientRequiredFields['formattedColumns']
                  : {}
              }
              columnExtensions={
                Object.keys(clientRequiredFields).length > 0
                  ? clientRequiredFields['columnExtensions']
                  : []
              }
            />
          ) : selectedTab === 1 ? (
            <TableComponent
              id="tab-partial"
              rows={
                Object.keys(mandatoryFields).length > 0
                  ? mandatoryFields['rows']
                  : []
              }
              columns={
                Object.keys(mandatoryFields).length > 0
                  ? mandatoryFields['columns']
                  : []
              }
              message="No available data"
              showSelectionColumn={false}
              onClickRow={(_row: number[]) => {}}
              onClickCell={handleIsRequiredClick}
              disableSelect
              disableSearch
              disableFilter
              disableSort
              disablePaging
              singleSelect={true}
              formattedColumns={
                Object.keys(mandatoryFields).length > 0
                  ? mandatoryFields['formattedColumns']
                  : {}
              }
              columnExtensions={
                Object.keys(mandatoryFields).length > 0
                  ? mandatoryFields['columnExtensions']
                  : []
              }
            />
          ) : null}
        </Grid>
        <Grid item xs={12}>
          <AddMemberFieldDrawer
            isOpen={isDrawerOpen}
            onSubmit={onDrawerSubmit}
            onClose={onDrawerClose}
          />
        </Grid>
        {props.hasChanges ? (
          <Grid item xs={12}>
            <FloatingButtons
              leftButtons={<SnackBarButton handleSave={handleSaveChanges} />}
            />
          </Grid>
        ) : null}
      </Grid>
    </div>
  );
};

const ButtonStyle = makeStyles({
  root: {
    height: '40px',
    minWidth: '150px',
    '&.Mui-disabled': {
      backgroundColor: '#E4E4E4',
      color: 'rgba(0, 0, 0, 0.61)',
    },
  },
  rightButton: {
    height: '40px',
    minWidth: '99px',
    backgroundColor: '#3C394A',
    color: '#FFFFFF',
  },
  leftButton: {
    padding: '0px 8px',
    height: '40px',
    width: '150px',
    border: '2px solid #3AB77D',
  },
  backButton: {
    padding: '0px 8px',
    height: '40px',
    width: '150px',
    backgroundColor: '#3C394A',
    color: '#FFFFFF',
    textTransform: 'none',
    '&:hover': {
      background: '#000000',
    },
  },
  leftText1: {
    fontSize: '13px',
  },
  leftText2: {
    fontSize: '7px',
  },
  buttonTitle: {
    fontWeight: 700,
    fontSize: 13,
  },
  buttonSubTitle: {
    fontWeight: 400,
    fontSize: 7,
  },
});

function SnackBarButton(props) {
  const buttonclasses = ButtonStyle();
  return (
    <div>
      <Button
        id={'upload_member_next_btn'}
        className={buttonclasses.root}
        variant="contained"
        color="primary"
        size="small"
        disabled={props.disabled}
        onClick={props.handleSave}
      >
        Save
      </Button>
    </div>
  );
}

SnackBarButton.propTypes = {
  handleSave: PropTypes.func,
  disabled: PropTypes.bool,
};
