import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Typography, Box } from '@material-ui/core/';
import { SITextField } from '../SITextField';
import { SICheckboxItem } from '../SICheckboxItem';
import { DraggableList } from '../DraggableList';
import { pull } from 'lodash';

const soa_gen_sort_list = [
    {
        code: 'site',
        label: 'Site',
    },
    {
        code: 'sub-company',
        label: 'Sub-company',
    },
    {
        code: 'cost-center',
        label: 'Cost Center',
    },
    {
        code: 'batch-no',
        label: 'Endorsement / Batch Number',
    },
    {
        code: 'employee-no',
        label: 'Employee No. / Employee ID',
    },
    {
        code: 'alphabetical',
        label: 'Alphabetical',
    },
    {
        code: 'member-plan-type',
        label: 'Member Plan Type',
    },
    {
        code: 'effectivity-date',
        label: 'Effectivity Date',
    },
    {
        code: 'principal-employees',
        label: 'Employees (Principal)',
    },
    {
        code: 'company-paid-dependents',
        label: 'Company-Paid Dependents',
    },
    {
        code: 'employee-paid-dependents',
        label: 'Employee-Paid Dependents',
    }
];

interface BillingProps {
    data: any;
    updateData: any;
}

const itemStyles = makeStyles({
    subheader: {
        fontSize: '1rem',
        color: '#272E4C'
    },
    description: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
});

export const Billing: React.FC<BillingProps> = (
    props: BillingProps,
): JSX.Element => {

    const classes = itemStyles(props);
    
    let soaFormatMap = {
        hard_copy: false,
        soft_copy_email: false,
        soft_copy_portal: false,
    };

    if(props.data && props.data['send_soa_formats']){
        props.data['send_soa_formats'].forEach(item => {
            if(item.format === 'hard copy'){
                soaFormatMap['hard_copy'] = true;
            }
            else if(item.format === 'soft copy'){
                soaFormatMap['soft_copy_email'] = item.options && item.options.indexOf('email') > -1;
                soaFormatMap['soft_copy_portal'] = item.options && item.options.indexOf('client portal') > -1;
            }
        });
    }

    const [soaFormatData, setSOAFormatData] = useState(soaFormatMap);

    const updateBillingData = (key: string, value: any) => {
        let currData = props.data ? props.data : {};
        currData[key] = value;
        props.updateData('billing', currData);
    };

    const handleCheckboxChange = (key: string, code: string, newValue: boolean) => {
        if(newValue){
            if(props.data && props.data[key]){
                updateBillingData(key, [...props.data[key], code]);
            }
            else{
                updateBillingData(key, [code]);
            }
        }
        else{
            if(props.data && props.data[key]){
                updateBillingData(key, [...pull(props.data[key], code)]);
            }
            else{
                updateBillingData(key, []);
            }
        }
    };

    const isChecked = (key: string, code: string) => {
        return props.data && props.data[key] && props.data[key].indexOf(code) > -1;
    };

    const handleSOAFormatChange = (code: string, newValue: string) => {
        let currSOAFormatData = soaFormatData;
        currSOAFormatData[code] = newValue;
        setSOAFormatData(currSOAFormatData);
        let soa_format: any[] = [];
        if(currSOAFormatData['hard_copy']){
            soa_format.push({
                format: 'hard copy'
            });
        }
        if(currSOAFormatData['soft_copy_email'] || currSOAFormatData['soft_copy_portal']){
            let options: string[] = [];
            if(currSOAFormatData['soft_copy_email']){
                options.push('email');
            }
            if(currSOAFormatData['soft_copy_portal']){
                options.push('client portal');
            }   
            soa_format.push({
                format: 'soft_copy',
                options: options
            });
        }

        updateBillingData('send_soa_formats', soa_format);
    }
    
    return (
        <div>
            <Grid container>
                <Grid item xs={12} style={{marginBottom: 24}}>
                    <Typography className={classes.subheader}>
                        Additional fields to be included in SOA attachment
                    </Typography>
                    <SICheckboxItem 
                        label="Site"
                        checked={isChecked('additional_fields_soa', 'site')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'site', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Sub-company"
                        checked={isChecked('additional_fields_soa', 'sub-company')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'sub-company', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Cost Center"
                        checked={isChecked('additional_fields_soa', 'cost center')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'cost center', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Endorsement / Batch Number"
                        checked={isChecked('additional_fields_soa', 'batch no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'batch no', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Employee No. / Employee ID"
                        checked={isChecked('additional_fields_soa', 'employee no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'employee no', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Cost c/o company / Employer"
                        checked={isChecked('additional_fields_soa', 'cost care of')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_fields_soa', 'cost care of', newValue);
                        }} />
                </Grid>
                <Grid item xs={12} md={6}>
                    <Typography className={classes.subheader}>
                        SOAs Generation
                    </Typography>
                    <Typography className={classes.description}>
                        Choose SOA generation and arrange their hierarchy
                    </Typography>
                    <DraggableList
                        list={soa_gen_sort_list}
                        selectedList={props.data && props.data['soa_generation'] ? props.data['soa_generation'] : []}
                        handleChange={newSelectedList => {
                            updateBillingData('soa_generation', newSelectedList);
                        }}
                     />
                </Grid>
                <Grid item xs={12} md={6}>
                    <Typography className={classes.subheader}>
                        SOAs Sorting
                    </Typography>
                    <Typography className={classes.description}>
                        Sort SOA according to options and arrange their hierarchy
                    </Typography>
                    <DraggableList
                        list={soa_gen_sort_list}
                        selectedList={props.data && props.data['soa_sorting'] ? props.data['soa_sorting'] : []}
                        handleChange={newSelectedList => {
                            updateBillingData('soa_sorting', newSelectedList);
                        }}
                     />
                </Grid>
                <Grid item xs={12} md={7} style={{paddingTop: 24}}>
                    <SITextField
                        label="Address SOA to"
                        value={props.data && props.data['address_soa_to'] ? props.data['address_soa_to'] : ''}
                        handleChange={(value) => {
                            updateBillingData('address_soa_to', value);
                        }} />
                    <SITextField
                        label="Send SOA to"
                        value={props.data && props.data['send_soa_to'] ? props.data['send_soa_to'] : ''}
                        handleChange={(value) => {
                            updateBillingData('send_soa_to', value);
                        }} />
                </Grid>
                <Grid item xs={12}>
                    <Typography className={classes.subheader}>
                        Send SOA on specific days only
                    </Typography>
                    <Box style={{marginLeft: 24}}>
                        <SICheckboxItem 
                            label="Monday"
                            checked={isChecked('send_soa_days', 'monday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'monday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Tuesday"
                            checked={isChecked('send_soa_days', 'tuesday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'tuesday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Wednesday"
                            checked={isChecked('send_soa_days', 'wednesday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'wednesday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Thursday"
                            checked={isChecked('send_soa_days', 'thursday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'thursday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Friday"
                            checked={isChecked('send_soa_days', 'friday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'friday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Saturday"
                            checked={isChecked('send_soa_days', 'saturday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'saturday', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Sunday"
                            checked={isChecked('send_soa_days', 'sunday')}
                            handleChange={(newValue) => {
                                handleCheckboxChange('send_soa_days', 'sunday', newValue);
                            }} />
                    </Box>
                </Grid>
                <Grid item xs={12}>
                    <Typography className={classes.subheader}>
                        Send SOA in the following formats
                    </Typography>
                    <SICheckboxItem 
                        label="Hard Copy"
                        checked={soaFormatData['hard_copy']}
                        handleChange={(newValue) => {
                            handleSOAFormatChange('hard_copy', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Soft Copy"
                        disabled={true}
                        checked={soaFormatData['soft_copy_email'] || soaFormatData['soft_copy_portal']}
                        handleChange={() => {}} />
                    <Box style={{marginLeft: 24}}>
                        <SICheckboxItem 
                            label="Email"
                            checked={soaFormatData['soft_copy_email']}
                            handleChange={(newValue) => {
                                handleSOAFormatChange('soft_copy_email', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Client Portal"
                            checked={soaFormatData['soft_copy_portal']}
                            handleChange={(newValue) => {
                                handleSOAFormatChange('soft_copy_portal', newValue);
                            }} />
                    </Box>
                        
                    
                </Grid>
                <Grid item xs={12} style={{paddingTop: 24}}>
                    <Typography className={classes.subheader}>
                        CM Application
                    </Typography>
                    <SICheckboxItem 
                        label="Separate SOA and CM"
                        checked={isChecked('cm_application', 'separate soa and cm')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('cm_application', 'separate soa and cm', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Automatic application / offset against current SOAs"
                        checked={isChecked('cm_application', 'automatic application')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('cm_application', 'automatic application', newValue);
                        }} />
                </Grid>
                <Grid item xs={12} style={{paddingTop: 24}}>
                    <Typography className={classes.subheader}>
                        Additional requirements
                    </Typography>
                    <SICheckboxItem 
                        label="Billing validation required prior to sending final SOA"
                        checked={isChecked('additional_requirements', 'billing validation')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_requirements', 'billing validation', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Premium Refund not to be released until full payment of premium"
                        checked={isChecked('additional_requirements', 'premium refund')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('additional_requirements', 'premium refund', newValue);
                        }} />
                </Grid>
            </Grid>
        </div>
    );
};