import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { 
    Grid, 
    Typography,
    FormControl,
    FormControlLabel,
    RadioGroup,
    Radio, 
    Box,
} from '@material-ui/core/';
import { SITextField } from '../SITextField';

interface CollectionProps {
  data: any;
  updateData: any;
}

const itemStyles = makeStyles({
    subheader: {
        fontSize: '1rem',
        color: '#272E4C'
    },
    description: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
});

export const Collection: React.FC<CollectionProps> = (
    props: CollectionProps,
): JSX.Element => {

    const classes = itemStyles(props);
    
    const updateCollectionData = (key: string, value: any) => {
        let currData = props.data ? props.data : {};
        currData[key] = value;
        props.updateData('collection', currData);
    };

    const onRadioBtnChange = event => {
        updateCollectionData('preferred_means_of_payment', event.target.value);
    };

    return (
        <div>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <SITextField
                        disabled={false}
                        label="Employee enrollment"
                        value={props.data && props.data['employee_enrollment'] ? props.data['employee_enrollment'] : ''}
                        handleChange={(value) => {
                            updateCollectionData('employee_enrollment', value);
                        }} />
                    <FormControl component="fieldset" margin="normal">
                        <Typography className={classes.subheader}>
                            Preferred means of payment
                        </Typography>
                        <RadioGroup 
                            value={props.data && props.data['preferred_means_of_payment'] ? props.data['preferred_means_of_payment'] : ''}
                            onChange={onRadioBtnChange}
                            name="preferred_means_of_payment">
                            <FormControlLabel
                                value="bank deposit"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Bank Deposit"}
                            />
                            {
                                props.data && props.data['preferred_means_of_payment'] === 'bank deposit' ?
                                (
                                    <Box style={{marginLeft: 24}}>
                                        <SITextField
                                            disabled={false}
                                            label="Preferred bank"
                                            value={props.data && props.data['preferred_bank'] ? props.data['preferred_bank'] : ''}
                                            handleChange={(value) => {
                                                updateCollectionData('preferred_bank', value);
                                            }} />
                                    </Box>
                                ) : ''
                            }
                            
                            <FormControlLabel
                                value="pick-up check"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Pick-up check"}
                            />
                            <FormControlLabel
                                value="deliver check"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Deliver check"}
                            />
                            <FormControlLabel
                                value="pick-up cash"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment"
                                    />
                                }
                                label={"Pick-up cash"}
                            />
                            <FormControlLabel
                                value="deliver cash"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Deliver Cash"}
                            />
                            <FormControlLabel
                                value="credit card"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Credit Card"}
                            />
                            <FormControlLabel
                                value="wire transfer"
                                control={
                                    <Radio 
                                        name="preferred_means_of_payment" />
                                }
                                label={"Wire Transfer"}
                            />
                        </RadioGroup>
                    </FormControl>
                </Grid>
                <Grid item xs={12} md={6}>
                    <SITextField
                        disabled={false}
                        label="Contact person for follow-ups"
                        value={props.data && props.data['contact_person_for_followups'] ? props.data['contact_person_for_followups'] : ''}
                        handleChange={(value) => {
                            updateCollectionData('contact_person_for_followups', value);
                        }} />
                    <SITextField
                        disabled={false}
                        label="Details for payment pick-up"
                        value={props.data && props.data['details_for_payment_pickup'] ? props.data['details_for_payment_pickup'] : ''}
                        handleChange={(value) => {
                            updateCollectionData('details_for_payment_pickup', value);
                        }} />
                </Grid>
            </Grid>
        </div>
    );
};