import React from 'react';
import { TableComponent } from './TableComponent/MemberListTable';
import { GlobalFunction } from 'Components/UI/GlobalFunction';
import { TextField, InputAdornment, Grid, Typography } from '@material-ui/core';
import { Search } from '@material-ui/icons/';
import { Loader } from './LoadingIndicator';

let API_URL = process.env.REACT_APP_BASE_URL;

interface IState {
  loading_state: boolean;
  member_list: any;
  member_data_all: any;
  member_data: any;
  selected_date_filter: any;
  selected_start_date: any;
  selected_end_date: any;
  global_filter: string;
  redirect_to_member: boolean;
  member_id: string;
  default_filters: any;
  isModalOpen: boolean;
  modalTitle: string;
  modalMessage: string;
  totalCount: any;
  typing: boolean;
  typingTimeout: any;
  page: number;
  limit: number;
  isPageSizeModal: boolean;
}

export class ClientMembers extends React.Component<any, IState> {

  constructor(props) {
    super(props);

    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'member_card_no',
          width: 200,
          wordWrapEnabled: true,
        },
        {
          columnName: 'member_name',
          width: 250,
          wordWrapEnabled: true,
        },
        {
          columnName: 'corporates',
          width: 200,
          wordWrapEnabled: true,
        },
        {
          columnName: 'effectivity_date',
          wordWrapEnabled: true,
        },
        {
          columnName: 'termination_date',
          wordWrapEnabled: true,
        },
        {
          columnName: 'status',
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'member_card_no',
          title: 'Member ID',
        },
        {
          name: 'member_name',
          title: 'Name',
        },
        {
          name: 'corporates',
          title: 'Corporate/s',
        },
        {
          name: 'effectivity_date',
          title: 'Effective Date',
        },
        {
          name: 'termination_date',
          title: 'Termination Date',
        },
        {
          name: 'status',
          title: 'Status',
        },
      ],
      rows: [],
    };

    this.state = {
      loading_state: true,
      member_list: [],
      member_data: table,
      member_data_all: table,
      selected_date_filter: {
        id: '',
        value: 'This Month',
        label: 'This Month',
      },
      selected_start_date: new Date(),
      selected_end_date: new Date(),
      global_filter: '',
      redirect_to_member: false,
      member_id: '',
      default_filters: [],
      isModalOpen: false,
      isPageSizeModal: false,
      modalTitle: '',
      modalMessage: '',
      totalCount: 0,
      typing: false,
      typingTimeout: 0,
      page: 0,
      limit: 10,
    };
  }





  componentDidMount() {
    this.initialData();
  }





  initialData = () => {
    this.getMembers(10, 0).then(response => {
      if (response && response.error === undefined) {
        // const array = response.concat(response);
        const table = this.generateDataForTable(response.members);
        this.setState({
          member_list: response,
          member_data_all: table,
          member_data: table,
          loading_state: false,
          totalCount: response.total.count,
          page: 0,
          limit: 10
        });
      } else {
        const table = this.generateDataForTable([]);
        this.setState({
          member_list: [],
          member_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Loading failed',
          modalMessage: 'An error occured while loading the list of members.',
          page: 0
        });
      }
    });
  }





  searchMembers = (limit: number, offset: number, filters: any, query: string) => {
    let CLIENT_URL = 
    `${API_URL}members/search?limit=${limit}&offset=${offset}&filters=${JSON.stringify(
        filters,
      )}&query=${query}&clientId=${this.props.clientId}`;
    if (limit === 0) CLIENT_URL = API_URL +
    `members/search?offset=${offset}&filters=${JSON.stringify(
      filters,
    )}&query=${query}`;
    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
      });
  }





  getMembers = (limit: number, skip: number) => {
    let filters = {
      where: {
        client_id: this.props.clientId
      },
      limit: limit,
      skip: skip,
    };

    const CLIENT_URL =
      API_URL + `members/?filter=${JSON.stringify(filters)}&count=${true}`;
    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
      });
  }





  generateDataForTable(response: any) {
    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'member_card_no',
          width: 200,
          wordWrapEnabled: true,
        },
        {
          columnName: 'member_name',
          width: 250,
          wordWrapEnabled: true,
        },
        {
          columnName: 'corporates',
          width: 200,
          wordWrapEnabled: true,
        },
        {
          columnName: 'effectivity_date',
          wordWrapEnabled: true,
        },
        {
          columnName: 'termination_date',
          wordWrapEnabled: true,
        },
        {
          columnName: 'status',
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'member_card_no',
          title: 'Member ID',
        },
        {
          name: 'member_name',
          title: 'Name',
        },
        {
          name: 'corporates',
          title: 'Corporate/s',
        },
        {
          name: 'effectivity_date',
          title: 'Effective Date',
        },
        {
          name: 'termination_date',
          title: 'Termination Date',
        },
        {
          name: 'status',
          title: 'Status',
        },
      ],
      rows: [],
    };
    for (const cnt in response) {
      const item = response[cnt];
      const temp_row: any = {};
      temp_row['member_card_no'] = item['member_card_no'];
      if (
        item['effectivity_date'] !== undefined &&
        item['effectivity_date'] !== null &&
        this.isValidDate(item['effectivity_date'])
      ) {
        temp_row['effectivity_date'] = GlobalFunction.toCommonDateString(
          new Date(item['effectivity_date']),
        );
      } else {
        temp_row['effectivity_date'] = '';
      }
      if (
        item['termination_date'] !== undefined &&
        item['termination_date'] !== null &&
        this.isValidDate(item['termination_date'])
      ) {
        temp_row['termination_date'] = GlobalFunction.toCommonDateString(
          new Date(item['termination_date']),
        );
      } else {
        temp_row['termination_date'] = '';
      }
      let member_name: string = '';
      if (
        item['first_name'] &&
        item['first_name'] !== undefined &&
        item['first_name'] !== null
      ) {
        member_name += item['first_name'];
      }
      if (
        item['middle_name'] &&
        item['middle_name'] !== undefined &&
        item['middle_name'] !== null
      ) {
        member_name += ' ' + item['middle_name'].substr(0, 1) + '.';
      }
      if (
        item['last_name'] &&
        item['last_name'] !== undefined &&
        item['last_name'] !== null
      ) {
        member_name += ' ' + item['last_name'];
      }
      if (
        item['client_object'] &&
        item['client_object'] !== undefined &&
        item['client_object'] !== null
      ) {
        const corporates: any[] = item['client_object'].map(item => {
          return item['registered_name'];
        });
        temp_row['corporates'] = corporates.join(', ');
      } else {
        temp_row['corporates'] = '';
      }
      if (
        item['member_status'] &&
        item['member_status'] !== undefined &&
        item['member_status'] !== null
      ) {
        temp_row['status'] = item['member_status'];
      } else {
        temp_row['status'] = '';
      }
      temp_row['member_name'] = member_name;
      temp_row['id'] = item['_id'];
      table['rows'].push(temp_row);
    }

    return table;
  }





  changePageUpdate = (limit: number, skip: number) => {
    this.setState({
      loading_state: true
    })
    if (this.state.default_filters.length !== 0 || this.state.global_filter !== '') {
      this.searchMembers(limit, skip, this.state.default_filters, this.state.global_filter).then(response => {
        if (response && response.error === undefined) {
          // const array = response.concat(response);
          const table = this.generateDataForTable(response.data);
          this.setState({
            member_list: response,
            member_data_all: table,
            member_data: table,
            loading_state: false,
            totalCount: response.totalCount,
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            member_list: [],
            member_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of members.'
          });
        }
      });
    } else {
      this.getMembers(limit, skip).then(response => {
        if (response && response.error === undefined) {
          // const array = response.concat(response);
          const table = this.generateDataForTable(response.members);
          this.setState({
            member_list: response,
            member_data_all: table,
            member_data: table,
            loading_state: false,
            totalCount: response.total.count
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            member_list: [],
            member_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of members.',
          });
        }
      });
    }
  }





  handleChangeSorting = (sorting: any) => {
    const { member_data, member_data_all } = this.state;
    let data = Object.assign({}, member_data);
    let all = Object.assign({}, member_data_all);
    console.log('sorting >>>>', sorting);

    if (sorting && sorting[0] && sorting[0]['columnName']) {
      switch (sorting[0]['columnName']) {
        case 'member_card_no':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) =>
              a.member_card_no > b.member_card_no ? 1 : -1,
            );
            all.rows.sort((a, b) =>
              a.member_card_no > b.member_card_no ? 1 : -1,
            );
          } else {
            data.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
            all.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
          }
          break;
        case 'member_name':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
            all.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
            all.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
          }
          break;
        case 'corporates':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.corporates > b.corporates ? 1 : -1));
            all.rows.sort((a, b) => (a.corporates > b.corporates ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.corporates > b.corporates ? -1 : 1));
            all.rows.sort((a, b) => (a.corporates > b.corporates ? -1 : 1));
          }
          break;
        case 'effectivity_date':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) =>
              a.effectivity_date > b.effectivity_date ? 1 : -1,
            );
            all.rows.sort((a, b) =>
              a.effectivity_date > b.effectivity_date ? 1 : -1,
            );
          } else {
            data.rows.sort((a, b) =>
              a.effectivity_date > b.effectivity_date ? -1 : 1,
            );
            all.rows.sort((a, b) =>
              a.effectivity_date > b.effectivity_date ? -1 : 1,
            );
          }
          break;
        case 'termination_date':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) =>
              a.termination_date > b.termination_date ? 1 : -1,
            );
            all.rows.sort((a, b) =>
              a.termination_date > b.termination_date ? 1 : -1,
            );
          } else {
            data.rows.sort((a, b) =>
              a.termination_date > b.termination_date ? -1 : 1,
            );
            all.rows.sort((a, b) =>
              a.termination_date > b.termination_date ? -1 : 1,
            );
          }
          break;
        case 'status':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.status > b.status ? 1 : -1));
            all.rows.sort((a, b) => (a.status > b.status ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.status > b.status ? -1 : 1));
            all.rows.sort((a, b) => (a.status > b.status ? -1 : 1));
          }
          break;
      }

      this.setState({
        member_data: data,
        member_data_all: all,
      });

      return all;
    }
  }





  handleFilterByColumn = (filter: any) => {
    const self = this;

    if (self.state.typingTimeout) {
       clearTimeout(self.state.typingTimeout);
    }

    self.setState({
       default_filters: filter,
       typing: false,
       page: 0,
       typingTimeout: setTimeout(function () {
        self.setState({
          loading_state: true
        })
          if (self.state.default_filters !== 0) {
            self.searchMembers(self.state.limit, 0, filter, self.state.global_filter).then(response => {
              if (response && response.error === undefined) {
                // const array = response.concat(response);
                const table = self.generateDataForTable(response.data);
                self.setState({
                  member_list: response,
                  member_data_all: table,
                  member_data: table,
                  loading_state: false,
                  totalCount: response.totalCount
                });
              } else {
                const table = self.generateDataForTable([]);
                self.setState({
                  member_list: [],
                  member_data_all: table,
                  loading_state: false,
                  isModalOpen: true,
                  modalTitle: 'Loading failed',
                  modalMessage: 'An error occured while loading the list of members.'
                });
              }
            });
          } else {
            self.initialData();
          }
         }, 2000)
    });

    // TODO add filter api call
  };





  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    const self = this;
    
    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
   }
    self.setState({
      global_filter: value,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
       self.setState({
         loading_state: true,
         limit: 10
       })
          if (self.state.global_filter !== "") {
            self.searchMembers(self.state.limit, 0, self.state.default_filters, self.state.global_filter).then(response => {
              if (response && response.error === undefined) {
                // const array = response.concat(response);
                const table = self.generateDataForTable(response.data);
                self.setState({
                  member_list: response,
                  member_data_all: table,
                  member_data: table,
                  loading_state: false,
                  totalCount: response.totalCount,
                  limit: 10
                });
              } else {
                const table = self.generateDataForTable([]);
                self.setState({
                  member_list: [],
                  member_data_all: table,
                  loading_state: false,
                  isModalOpen: true,
                  modalTitle: 'Loading failed',
                  modalMessage: 'An error occured while loading the list of members.'
                });
              }
            });
          } else {
            self.initialData();
          }
        }, 2000)
   });
  }





  handleClickRow = (row: any) => {
    const { member_data } = this.state;
    const member_id = member_data['rows'][row[0]]['id'];
    if (member_id) {
      this.setState({
        redirect_to_member: true,
        member_id: member_id,
      });
    }
  }





  handlePageChange = (currentPage: number, skip: number) => {
    this.setState({
      page: currentPage
    });

    this.changePageUpdate(this.state.limit, skip);
  }





  handlePageSizeChange = (value) => {
    let pageLimit = 1000;

    if (this.state.totalCount >= pageLimit) {
      this.setState({
        isPageSizeModal: true
      })
    } else {
      this.setState({
        limit: value
      })
  
      this.changePageUpdate(value, (value * this.state.page));
    }
  }





  isValidDate = (date: string) => {
    const d = new Date(date);
    return d instanceof Date && !isNaN(d.getTime());
  }





  renderTopbar = () => {
    const mainStyle: React.CSSProperties = {
      padding: '1rem'
    };

    const itemStyle: React.CSSProperties = {
      flex: 'auto'
    }

    const valueStyle: React.CSSProperties = {
      color: '#3AB77D',
      fontWeight: 'bold',
      fontSize: '1rem'
    };

    const labelStyle: React.CSSProperties = {
      fontSize: '0.8rem'
    };

    const renderItem = (value: number, label: string) => {
      return (
        <Grid item xs style={itemStyle}>
          <Typography align="center">
            <span style={valueStyle}>{value}</span>
            <span style={labelStyle}>&nbsp;&nbsp;&nbsp;{label}</span>
          </Typography>
        </Grid>
      );
    }

    return (
      <Grid container xs={12} style={mainStyle}>
        {renderItem(1000, 'TOTAL')}
        {renderItem(890, 'ACTIVE')}
        {renderItem(36, 'AWAITING ACTIVATION')}
        {renderItem(25, 'FOR VALIDATION')}
        {renderItem(2, 'SUSPENDED')}
        {renderItem(18, 'TERMINATED')}
      </Grid>
    );
  }





  render(): JSX.Element {

    return (
      <div>
        {this.state.loading_state ? <Loader /> : null}
        <TextField
          style={{ width: '100%' }}
          id="members_search"
          placeholder="Search members by typing their member ID, name, etc."
          label=""
          margin="normal"
          variant="outlined"
          value={this.state.global_filter}
          onChange={this.handleSearch}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
              </InputAdornment>
            ),
          }}
        />
        <div style={{padding: '1rem'}} />
        <TableComponent
          id="tab-all"
          totalCount={this.state.totalCount}
          update={this.changePageUpdate}
          updateSorting={this.handleChangeSorting}
          // topBar={() => this.renderTopbar()} // UNCOMMENT IF ALREADY HAVE ENDPOINTS
          rows={
            Object.keys(this.state.member_data).length > 0 ? 
              this.state.member_data['rows'] : 
              []
          }
          columns={
            Object.keys(this.state.member_data).length > 0 ? 
              this.state.member_data['columns'] : 
              []
          }
          message={
            this.state.global_filter
              ? 'No available data'
              : 'Start looking for members by typing in a member name, ID, or using the table filters.'
          }
          onClickRow={this.handleClickRow}
          disableSelect
          disableSearch
          formattedColumns={
            Object.keys(this.state.member_data).length > 0 ? 
            this.state.member_data['formattedColumns'] : 
            {}
          }
          columnExtensions={
            Object.keys(this.state.member_data).length > 0 ? 
            this.state.member_data['columnExtensions'] : 
            []
          }
          onFilterChange={this.handleFilterByColumn}
          onSortingChange={this.handleChangeSorting}
          onPageChange={this.handlePageChange}
          ongPageSizeChange={this.handlePageSizeChange}
          defaultFilter={this.state.default_filters}
          currentPage={this.state.page}
          limit={this.state.limit}
        />
      </div>
    )
  }
};