import React from 'react';
import {
	BasicTextField,
} from 'Components/UI/';
import { clientInformationStyles } from '../../ClientProfilePage'
import { MaterialUiPickersDate } from '@material-ui/pickers';
import { KeyboardDatePicker } from '@material-ui/pickers';
import {
	Grid,
	FormControlLabel,
	Typography,
	RadioGroup,
	Radio,
	InputLabel,
} from '@material-ui/core';


interface ClientInformation {
	invalidFields: string[];
	clientInfo?:any;
	setEditInfo?:(e) => void;
	setData:any;
	setSideNav?:(e)=>void;
}

export const EditHMO = (props:ClientInformation) => {
	const classes = clientInformationStyles();
	const handleTextFieldOnChange = (name: string , subname?:string) => (event: any) => {
		let data = {};
		if(subname!==undefined)
		{
			props.clientInfo.sales_channel[subname]= event.target.value ;
			data = { ...props.clientInfo};
		}
		else
		{
			data = { ...props.clientInfo, [name]: event.target.value };
		}
		if (props.setData) {
			props.setData(data);
		}
	};
	const handleDatePickerOnChange = (name: string, date: MaterialUiPickersDate | null) => {
		const data = { ...props.clientInfo, [name]: date ? date.toDate() : null };
		if (props.setData) {
			props.setData(data);
		}
	};
	return (
		<Grid container>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="previousHMOProvider"
					label="Previous HMO Provider*"
					value={props.clientInfo.prev_hmo_provider}
					onChange={handleTextFieldOnChange('prev_hmo_provider')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
					<InputLabel htmlFor="effectivityDate" className={classes.inputLabel}>
						Effectivity Date
					</InputLabel>
					<div style={{ display: 'flex', alignItems: 'center' }}>
						<KeyboardDatePicker
							id="effectivityDate"
							variant="dialog"
							format="MM/DD/YYYY"
							inputVariant="outlined"
							className={classes.keyboardDatePicker}
							value={props.clientInfo.prev_hmo_effectivity_date
									? props.clientInfo.prev_hmo_effectivity_date
									: ''}
							invalidDateMessage=""
							onChange={date => handleDatePickerOnChange('prev_hmo_effectivity_date', date)}
						/>
						<Typography display="inline">
							&nbsp;&nbsp;&nbsp;&mdash;&nbsp;&nbsp;&nbsp;
						</Typography>
						<KeyboardDatePicker
							id="expiryDate"
							variant="dialog"
							format="MM/DD/YYYY"
							inputVariant="outlined"
							className={classes.keyboardDatePicker}
							value={props.clientInfo.prev_hmo_expiry_date
									? props.clientInfo.prev_hmo_expiry_date
									: ''}
							invalidDateMessage=""
							onChange={date => handleDatePickerOnChange('prev_hmo_expiry_date', date)}
						/>
					</div>
			</Grid>
			<Grid item xs={12} className={classes.editContainer}>
			<RadioGroup
						value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
							&& props.clientInfo.sales_channel.type !== undefined
								? props.clientInfo.sales_channel.type
								: ''}
						onChange={handleTextFieldOnChange('sales_channel','type')}
					>
						
						<FormControlLabel
							value="agent"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Agent"
						/>
						<FormControlLabel
							value="broker"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Broker"
						/>
						
					</RadioGroup>
				
					
			</Grid>
			{props.clientInfo.sales_channel && props.clientInfo.sales_channel !== undefined
				&& props.clientInfo.sales_channel.type === 'agent' ? (
				<Grid item xs={6} className={classes.editContainer}>
					<BasicTextField
						id="agentName"
						label="Agent*"
						value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
							&& props.clientInfo.sales_channel.agent_name !== undefined
								? props.clientInfo.sales_channel.agent_name
								: ''}
						onChange={handleTextFieldOnChange('sales_channel','agent_name')}
					/>
				</Grid>	
			) : null}
			{props.clientInfo.sales_channel && props.clientInfo.sales_channel !== undefined 
				&& props.clientInfo.sales_channel.type === 'broker' ? (
				<React.Fragment>
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="brokerCompanyName"
							label="Name of Company*"
							value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
								&& props.clientInfo.sales_channel.company_name !== undefined
								? props.clientInfo.sales_channel.company_name
								: ''}
							onChange={handleTextFieldOnChange('sales_channel','company_name')}
						/>
					</Grid>
					
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="brokerContactPerson"
							label="Contact Person*"
							value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
								&& props.clientInfo.sales_channel.contact_person !== undefined
								? props.clientInfo.sales_channel.contact_person
								: ''}
							onChange={handleTextFieldOnChange('sales_channel','contact_person')}
						/>
					</Grid>
					
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="brokerPosition"
							label="Position*"
							value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
								&& props.clientInfo.sales_channel.position !== undefined
								? props.clientInfo.sales_channel.position
								: ''}
							onChange={handleTextFieldOnChange('sales_channel','position')}
						/>
					</Grid>
					
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="brokerContactNumber"
							label="Contact Number*"
							value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
								&& props.clientInfo.sales_channel.contact_no !== undefined
								? props.clientInfo.sales_channel.contact_no
								: ''}
							onChange={handleTextFieldOnChange('sales_channel','contact_no')}
						/>
					</Grid>
					<Grid item xs={6} className={classes.editContainer}>
						<BasicTextField
							id="brokerEmailAddress"
							label="Email Address*"
							value={props.clientInfo.sales_channel && Object.keys(props.clientInfo.sales_channel).length > 0 
								&& props.clientInfo.sales_channel.email_address !== undefined
								? props.clientInfo.sales_channel.email_address
								: ''}
							onChange={handleTextFieldOnChange('sales_channel','email_address')}
						/>
					</Grid>
				</React.Fragment>
			) : null}
			<Grid item xs={6} className={classes.editContainer}>
							
			</Grid>
		</Grid>
	);
};
