import React from 'react';
import { AuditLogsTable } from './AuditLogsTable';
import { AuditLogsState } from './AuditLogsInterface';
import { API } from 'Pages/API';
import { LoadingIndicator } from 'Components/UI/';
// IU imports
import {
    Grid,
    TextField
} from '@material-ui/core';
import { GridHorizontalDivider } from 'Components/UI';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faSearch,
    faDownload
  } from '@fortawesome/free-solid-svg-icons';
import Link from '@material-ui/core/Link';
import moment from 'moment';

export class AuditLogs extends React.Component<{classes: any}, AuditLogsState> {
    constructor(props) {
        super(props);

        this.state = {
            pageSizes: [5, 10, 15],
            currentPage: 0,
            pageSize: 10,
            totalCount: 25,
            skip: 0,
            typing: false,
            typingTimeout: 0,
            order: [],
            sorting: [],
            loading: false,
            filter: [],
            query: '',
            columns: [
                { name: 'activity_id', title: 'Activity ID' },
                { name: 'activity', title: 'Activity' },
                { name: 'user', title: 'User' },
                { name: 'date', title: 'Date' },
                { name: 'time', title: 'Time' }
            ],
            rows: [
                {
                    activity_id: '090120',
                    activity: 'Updated home address',
                    user: 'Romina O. Sanchez',
                    date: 'Dec 3, 2018',
                    time: '12:03:03 PM'
                  
                }
            ]
        }
    }

    componentWillMount = () => {
        this.fetchAuditLogs();
    }

    fetchAuditLogs = () => {
        let { pageSize, skip, filter, sorting } = this.state;

        this.setState({
            loading: true
        }, () => {
            if (this.state.filter[0]) {
                API.searchAuditLogs(this.getClientId(), pageSize, skip, filter, sorting).then((response) => {
                    if (response && !response.error) {
                        this.setState({
                            rows: response.audit_logs,
                            totalCount: response.total.count,
                            loading: false
                        });
                    } else {
                        this.setState({loading: false});
                    }
                }).catch(() => this.setState({loading: false}))
            } else {
                API.getAuditLogs(this.getClientId(), pageSize, skip, sorting).then((response) => {
                    if (response && !response.error) {
                        this.setState({
                            rows: response.audit_logs,
                            totalCount: response.total.count,
                            loading: false
                        });
                    } else {
                        this.setState({loading: false});
                    }
                }).catch(() => this.setState({loading: false}))
            }
        });
    }

    fetchFilteredAuditLogs = () => {
        let { pageSize, skip, filter, sorting, query } = this.state;

        this.setState({
            loading: true
        }, () => {
            if (this.state.query) {
                API.searchAuditLogs(this.getClientId(), pageSize, skip, filter, query, sorting).then((response) => {
                    if (response && !response.error) {
                        this.setState({
                            rows: response.audit_logs,
                            totalCount: response.total.count,
                            loading: false
                        })
                    } else {
                        this.setState({loading: false})
                    }
                }).catch(error => console.log(error))
            }  else {
                API.searchAuditLogs(this.getClientId(), pageSize, skip, filter, sorting).then((response) => {
                    if (response && !response.error) {
                        this.setState({
                            rows: response.audit_logs,
                            totalCount: response.total.count,
                            loading: false
                        })
                    } else {
                        this.setState({loading: false})
                    }
                }).catch(error => console.log(error))
            }
        })
    }

    getClientId = () => {
        let url = window.location.href;
        return url.substring(url.lastIndexOf('/') + 1)
    }

    onFilter = (filter) => {
        console.log(filter);
    }

    onSortingChange = (sorting) => {
        this.setState({
            loading: true,
            sorting: this.sortingFormatter(sorting)
        }, () => {
            if (this.state.filter || this.state.sorting || this.state.query) this.fetchFilteredAuditLogs()
            else this.fetchAuditLogs();
        })
    }

    changePageSize = (value) => {
        this.setState({
            pageSize: value,
            loading: true
        }, () => {
            if (this.state.filter || this.state.sorting || this.state.query) this.fetchFilteredAuditLogs()
            else this.fetchAuditLogs();
        })
    }

    onFilterChange = (filter) => {
        const self = this;
    
        if (self.state.typingTimeout) {
            clearTimeout(self.state.typingTimeout);
        }

        filter.find(a => {
            if (a.type === 'date') {
                a.value = moment(a.value).format("MMM DD, YYYY");
            }

            if (a.type === 'time') {
                let time = moment(a.value).format("h:mm:ss A");
                if (time !== 'Invalid date') a.value = moment(a.value).format("h:mm:ss A");
                
            }
        })

        if (this.state.query) {
            self.setState({
                typing: false,
                filter: filter,
                skip: 0,
                typingTimeout: setTimeout( () => {
                    self.setState({
                        loading: true
                    }, () => {
                        API.searchAuditLogs(self.getClientId(), self.state.pageSize, self.state.skip, filter, self.state.query, self.state.sorting).then((response) => {
                            if (response && !response.error) {
                                self.setState({
                                    rows: response.audit_logs,
                                    totalCount: response.total.count,
                                    currentPage: 0,
                                    loading: false
                                })
                            } else {
                                self.setState({loading: false})
                            }
                        }).catch(error => console.log(error))   
                    })      
                }, 2000)
            })
        } else {
            self.setState({
                typing: false,
                filter: filter,
                skip: 0,
                typingTimeout: setTimeout( () => {
                    self.setState({
                        loading: true
                    }, () => {
                        API.searchAuditLogs(self.getClientId(), self.state.pageSize, self.state.skip, filter, self.state.sorting).then((response) => {
                            if (response && !response.error) {
                                self.setState({
                                    rows: response.audit_logs,
                                    totalCount: response.total.count,
                                    currentPage: 0,
                                    loading: false
                                })
                            } else {
                                self.setState({loading: false})
                            }
                        }).catch(error => console.log(error))   
                    })      
                }, 2000)
            })
        }
    }

    globalFilter = (e) => {
        const self = this;

        let value = e.target.value

        if (self.state.typingTimeout) {
            clearTimeout(self.state.typingTimeout);
        }

        self.setState({
            typing: false,
            query: value,
            skip: 0,
            typingTimeout: setTimeout( () => {
                self.setState({
                    loading: true
                }, () => {
                    if (value !== '') {
                        API.searchAuditLogs(self.getClientId(), self.state.pageSize, self.state.skip, self.state.filter, value, self.state.sorting).then((response) => {
                            if (response && !response.error) {
                                self.setState({
                                    rows: response.audit_logs,
                                    totalCount: response.total.count,
                                    currentPage: 0,
                                    loading: false
                                })
                            } else {
                                self.setState({loading: false})
                            }
                        }).catch(error => console.log(error)) 
                
                    } else {
                        self.fetchAuditLogs();
                    }
                })      
            }, 2000)
        })
    }

    onCurrentPageChange = (page) => {
        let skip = (this.state.pageSize * page);

        this.setState({
            currentPage: page,
            skip: skip
        }, () => {
            
            if (this.state.filter[0] || this.state.sorting[0] || this.state.query) this.fetchFilteredAuditLogs()
            else this.fetchAuditLogs();
        });
    }

    sortingFormatter = (order: Array<object>) => {
        console.log(order[0]);
        return Object.values(order[0]);
    }

    render(): JSX.Element {

        const {
            rows,
            columns,
            pageSizes,
            currentPage,
            pageSize,
            totalCount,
            loading
        } = this.state;

        const { classes } = this.props;

        return (
            <>
            { loading ?
                (<LoadingIndicator/>) : null

            }
                <>
                <Grid container>
                    <GridHorizontalDivider height="30px" />
                        <Grid item sm={9}>
                            <TextField
                                onChange={this.globalFilter}
                                id="history_search"
                                placeholder="Search log"
                                className={classes.searchField}
                                margin="dense"
                                variant="outlined"
                                fullWidth
                                inputProps={{ 'aria-label': 'bare' }}
                                InputProps={{
                                startAdornment: (
                                    <FontAwesomeIcon
                                    icon={faSearch}
                                    className={classes.searchInputAdornment}
                                    />
                                ),
                                }}
                            />
                        </Grid>
                        <Grid item sm={3} className={classes.generateReport}>
                            <FontAwesomeIcon icon={faDownload} className={classes.downloadIcon} />
                            <Link className={classes.linkStyleSelected}>Generate Report</Link>
                        </Grid>
                        <Grid item xs={12} className={classes.gridItem} style={{marginTop: '15px'}}>
                            <AuditLogsTable 
                                rows={rows}
                                columns={columns}
                                pageSizes={pageSizes}
                                currentPage={currentPage}
                                pageSize={pageSize}
                                totalCount={totalCount}
                                onFilter={this.onFilter}
                                onSortingChange={this.onSortingChange}
                                changePageSize={this.changePageSize}
                                onFilterChange={this.onFilterChange}
                                onCurrentPageChange={this.onCurrentPageChange} />
                        </Grid>
                    </Grid>
                </>
            </>
        )
    }
}
