import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { 
    Grid, 
    Typography,
    FormControl,
    FormControlLabel,
    RadioGroup,
    Radio, 
} from '@material-ui/core/';
import { SICheckboxItem } from '../SICheckboxItem';
import { DraggableList } from '../DraggableList';
import { pull } from 'lodash';

const itemStyles = makeStyles({
    subheader: {
        fontSize: '1rem',
        color: '#272E4C'
    },
    description: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
    noDataMessage: {
        fontSize: '0.875rem',
        color: '#272E4C',
        fontWeight: 600,
        textAlign: 'center',
    },
});

interface SortingOfCardsProps {
    memberFields: any[];
    data: any;
    updateData: any;
}

export const SortingOfCards: React.FC<SortingOfCardsProps> = (
    props: SortingOfCardsProps,
): JSX.Element => {

    const classes = itemStyles(props);

    const updateSortingData = (key: string, value: any) => {
        let currData = props.data ? props.data : {};
        currData[key] = value;
        props.updateData('sorting_of_cards', currData);
    };
    
    return (
        <div>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Typography className={classes.subheader}>
                        Card Sorting
                    </Typography>
                    <Typography className={classes.description}>
                        Choose category for sorting and arrange their hierarchy
                    </Typography>
                    {
                        props.memberFields && props.memberFields.length > 0 ?
                        (
                            <DraggableList
                                list={props.memberFields}
                                selectedList={props.data && props.data['card_sorting'] ? props.data['card_sorting'] : []}
                                handleChange={newSelectedList => {
                                    updateSortingData('card_sorting', newSelectedList);
                                }}
                            />
                        ) :
                        (
                            <Typography className={classes.noDataMessage} style={{marginTop: 48, marginRight: 64}}>
                                No required fields to display.
                            </Typography>
                        )
                    }
                </Grid>
                <Grid item xs={12} md={6}>
                    <Typography className={classes.subheader}>
                        Card Packing
                    </Typography>
                    <FormControl component="fieldset" margin="normal">
                        <RadioGroup 
                            value={props.data && props.data['card_packing'] ? props.data['card_packing'] : ''}
                            onChange={(event) => {
                                updateSortingData('card_packing', event.target.value);
                            }}
                            name="card_packing">
                            <FormControlLabel
                                value="envelope"
                                control={
                                    <Radio 
                                        name="card_packing" />
                                }
                                label={"Envelope"}
                            />
                            <FormControlLabel
                                value="plastic"
                                control={
                                    <Radio 
                                        name="card_packing" />
                                }
                                label={"Plastic"}
                            />
                            <FormControlLabel
                                value="with flyer"
                                control={
                                    <Radio 
                                        name="card_packing" />
                                }
                                label={"With Flyer"}
                            />
                        </RadioGroup>
                    </FormControl>
                    <Typography className={classes.subheader} style={{marginTop: 32, marginBottom: 8}}>
                        Pack according to
                    </Typography>
                    <SICheckboxItem 
                        label="Team Leader"
                        checked={props.data && props.data['pack_accdg_to'] && props.data['pack_accdg_to'].indexOf('team_leader') > -1}
                        handleChange={(newValue) => {
                            if(newValue){
                                if(props.data && props.data['pack_accdg_to']){
                                    updateSortingData('pack_accdg_to', [...props.data['pack_accdg_to'], 'team_leader']);
                                }
                                else{
                                    updateSortingData('pack_accdg_to', ['team_leader']);
                                }
                            }
                            else{
                                if(props.data && props.data['pack_accdg_to']){
                                    updateSortingData('pack_accdg_to', [...pull(props.data['pack_accdg_to'], 'team_leader')]);
                                }
                                else{
                                    updateSortingData('pack_accdg_to', []);
                                }
                            }
                        }} />
                    <SICheckboxItem 
                        label="Family"
                        checked={props.data && props.data['pack_accdg_to'] && props.data['pack_accdg_to'].indexOf('family') > -1}
                        handleChange={(newValue) => {
                            if(newValue){
                                if(props.data && props.data['pack_accdg_to']){
                                    updateSortingData('pack_accdg_to', [...props.data['pack_accdg_to'], 'family']);
                                }
                                else{
                                    updateSortingData('pack_accdg_to', ['family']);
                                }
                            }
                            else{
                                if(props.data && props.data['pack_accdg_to']){
                                    updateSortingData('pack_accdg_to', [...pull(props.data['pack_accdg_to'], 'family')]);
                                }
                                else{
                                    updateSortingData('pack_accdg_to', []);
                                }
                            }
                        }} />
                </Grid>
            </Grid>
        </div>
    );
};