const columns: any = [
    { 
        name: 'report_type',
        title: 'Report Type',
    },
    {
        name: 'frequency',
        title: 'Frequency',
    },
    { 
        name: 'available', 
        title: 'Available?',
    },
];

const formattedColumns = {
    text: ['report_type'],
    frequency: ['frequency'],
    isavailable: ['available'],
};

const columnExtensions = [
    { columnName: 'report_type', wordWrapEnabled: true },
    { columnName: 'frequency', wordWrapEnabled: true },
    { columnName: 'available', width: 150, align: 'center', wordWrapEnabled: true }
];

interface ReportorialProps {
    data: any[];
    updateData: any;
}

export const Reportorial: React.FC<ReportorialProps> = (
    props: ReportorialProps,
): JSX.Element => {

    const generateReportorialMap = () => {
        let propsMap: any = {};
        if(props.data){
            props.data.forEach(item => {
                let d = {...item};
                delete d.report_type
                propsMap[item.report_type] = d;
            });
        }
        let reportorialMap = dataMap.map(item => {
            if(propsMap && propsMap[item.report_type]){
                if(propsMap[item.report_type]['frequency']){
                    item['frequency'] = propsMap[item.report_type]['frequency'];
                }
                if(propsMap[item.report_type]['available']){
                    item['available'] = propsMap[item.report_type]['available'];
                }
            }
            return item;
        });

        return reportorialMap;
    }

    const updateReportorialData = (report_type: string, key: string, value: any) => {
        let currData: any[] = generateReportorialMap();
        let index = currData.findIndex(item => {
            return item['report_type'] === report_type;
        });

        currData[index][key] = value;

        props.updateData('reportorial', currData);
    }

    return (
        <div>
            <Grid container>
                <Grid item xs={12}>
                    <ReportorialTable
                        rows={generateReportorialMap()}
                        columns={columns}
                        formattedColumns={formattedColumns}
                        columnExtensions={columnExtensions}
                        updateReportorialData={updateReportorialData} />
                </Grid>
            </Grid>
        </div>
    );
};