import React, { useState, } from 'react';
import { Grid, Tabs, Tab, Button, } from '@material-ui/core/';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

import './style.scss';
import { FloatingButtons } from 'Components/UI/FloatingButtons';
import { SortingOfCards } from './SortingOfCards';
import { PrintingOfCards } from './PrintingOfCards';
import { Billing } from './Billing';
import { Enrollment } from './Enrollment';
import { Collection } from './Collection';
import { Availment } from './Availment';
import { Reportorial } from './Reportorial';

const page_tab_titles: any = [
    "Sorting of Cards",
    "Printing of Cards",
    "Billing",
    "Enrollment",
    "Collection",
    "Availment",
    "Reportorial",
]

const FooterButtonStyle = makeStyles({
    leftButton1: {
      padding: '0px 8px',
      height: '40px',
      width: '150px',
      backgroundColor: '#3AB77D62',
      border: '2px solid #3AB77D',
    },
    leftButton2: {
      padding: '0px 8px',
      height: '40px',
      width: '150px',
      border: '2px solid #3AB77D',
    },
});
  
function FooterButtons(props) {
    const buttonclasses = FooterButtonStyle();
    return (
      <div>
        <Button
          id={'save_si_btn'}
          className={buttonclasses.leftButton2}
          variant="contained"
          color="primary"
          size="small"
          onClick={props.handleSave}
        >
          Save
        </Button>
      </div>
    );
}

const ServicingInstructionsTabs = withStyles({
  root: {},
  indicator: {
    display: 'none',
  },
})(Tabs);

const ServicingInstructionsTab = withStyles({
  root: {
    color: 'rgba(39, 46, 76, 0.5)',
    minWidth: 80,
    paddingLeft: 24,
    paddingRight: 24,
    fontSize: '0.875rem',
    fontWeight: 400,
    '&:hover': {
      color: 'rgba(39, 46, 76, 1)',
      fontWeight: 400,
    },
    '&$selected': {
      textDecoration: 'underline',
      color: '#3AB77D',
      fontWeight: 600,
    },
  },
  selected: {},
})(Tab);

interface ServicingInstructionsProps {
  data: any;
  memberFields: any;
  setSIData?: any;
  saveSIData?: any;
}

export const ServicingInstructions: React.FC<ServicingInstructionsProps> = (
    props: ServicingInstructionsProps,
): JSX.Element => {
  
  const [selectedTab, setSelectedTab] = useState(0);
  const [siData, setSIData] = useState((props.data ? props.data : {}));

  const updateSIDataByTab = (tabkey: string, value: any) => {
    let currSIData = siData;
    currSIData[tabkey] = value;
    setSIData({...currSIData});
    console.log('new siData ', siData);
    props.setSIData(currSIData);
  }

  const handleTabChange = (
    _event: React.ChangeEvent<HTMLDivElement>,
    tab: number,
  ) => {
    setSelectedTab(tab);
  };

  return (
    <div className={clsx('ServicingInstructions')}>
        <Grid container>
          <Grid item xs={12} className={clsx('page-tab-title')}>
            <ServicingInstructionsTabs
              value={selectedTab}
              onChange={handleTabChange}
              aria-label="page tab"
            >
              {page_tab_titles.map((title: string, index: number) => (
                <ServicingInstructionsTab
                  label={title}
                  id={'servicing_instructions_tab_' + index}
                  aria-controls={'servicing-instructions-tabpanel-' + index}
                  key={index}
                />
              ))}
            </ServicingInstructionsTabs>
            {/* <div className={clsx('tab-title-shadow')} /> */}
          </Grid>
          <Grid item xs={12} className={clsx('page-tab-content')}>
            {selectedTab === page_tab_titles.indexOf('Sorting of Cards') ? (
              <SortingOfCards
                memberFields={props.memberFields}
                data={siData && siData['sorting_of_cards'] ? siData['sorting_of_cards'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Printing of Cards') ? (
              <PrintingOfCards
                data={siData && siData['printing_of_cards'] ? siData['printing_of_cards'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Billing') ? (
              <Billing
                data={siData && siData['billing'] ? siData['billing'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Enrollment') ? (
              <Enrollment
                data={siData && siData['enrollment'] ? siData['enrollment'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Collection') ? (
              <Collection 
                data={siData && siData['collection'] ? siData['collection'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Availment') ? (
              <Availment
                data={siData && siData['availment'] ? siData['availment'] : null}
                updateData={updateSIDataByTab} />
            ) : selectedTab === page_tab_titles.indexOf('Reportorial') ? (
              <Reportorial
                data={siData && siData['reportorial'] ? siData['reportorial'] : null}
                updateData={updateSIDataByTab} />
            ) : null}
          </Grid>
          <Grid item xs={12}>
            <FloatingButtons
            leftButtons={
                <FooterButtons
                    handleSave={() => {
                        props.saveSIData();
                    }} />
            }
            rightButtons={null}
            />
          </Grid>
        </Grid>
      </div>
  );
};

