import React from 'react';
import { Grid } from '@material-ui/core/';
import { SICheckboxItem } from '../SICheckboxItem';

interface AvailmentProps {
  data: any;
  updateData: any;
}

export const Availment: React.FC<AvailmentProps> = (
    props: AvailmentProps,
): JSX.Element => {
    return (
        <div>
            <Grid container>
                <Grid item xs={12}>
                    <SICheckboxItem 
                        label="Inform member of copayment / coinsurance share"
                        checked={props.data && props.data.indexOf('inform member of copayment') > -1}
                        handleChange={(newValue) => {
                            if(newValue){
                                props.updateData('availment', ['inform member of copayment']);
                            }
                            else{
                                props.updateData('availment', []);
                            }
                        }} 
                        />
                </Grid>
            </Grid>
        </div>
    );
};