const ClientsFilterCell = (rowData) => {
    const { column, filter, onFilter} = rowData;
    const datePickerClasses = datePickerStyles();
    
    if (column.name === 'date') {
        return (
            <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }}>
                <KeyboardDatePicker
                    variant="dialog"
                    format="MMMM DD, YYYY"
                    inputVariant="outlined"
                    className={datePickerClasses.keyboardDatePicker}
                    value={filter ? filter.value : ''}
                    invalidDateMessage=""
                    onChange={date => {console.log(date); onFilter(date ? { value: date, type: "date" , raw: date} : null)}}
                />
            </MaterialUI.TableCell>
        );
    }

    if (column.name === 'time') {
        return (
            <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }}>
                <TimePicker
                    openTo="hours"
                    views={["hours", "minutes", "seconds"]}
                    format="h:mm:ss A"
                    className={datePickerClasses.keyboardDatePicker}
                    inputVariant="outlined"
                    value={filter ? filter.raw : null}
                    onChange={time => {console.log(time); onFilter(time ? { value: time, type: "time", raw: time } : null)}}
                />
            </MaterialUI.TableCell>
        )
    }
    
    return (
        <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} >
            <MaterialUI.OutlinedInput
                style={{ width: '100%' }}
                value={filter ? filter.value : ''}
                labelWidth={0}
                onChange={e => onFilter(e.target.value ? { value: e.target.value, type: "string", raw: {} } : null)}
                placeholder=""
            />
        </MaterialUI.TableCell>
    )
}

export class AuditLogsTable extends React.Component<AuditLogsProps, {}> {
    constructor(props: AuditLogsProps) {
        super(props);
    }

    onSortingChange = () => {
        
    }

    componentWillMount = () => {
        console.log(this.props.columns);
    }


    render(): JSX.Element {

        const {
            rows,
            columns,
            pageSizes,
            currentPage,
            pageSize,
            totalCount
        } = this.props;

        return (
            <>
                <Grid rows={rows} columns={columns}>
                    <PagingState
						currentPage={currentPage}
						onCurrentPageChange={this.props.onCurrentPageChange}
						pageSize={pageSize}
            			onPageSizeChange={this.props.changePageSize}
					/>

                    <CustomPaging totalCount={totalCount} />

                    <SortingState 
						defaultSorting={[]} 
						// sorting={sorting}
						onSortingChange={this.props.onSortingChange}
					/>

                    {/* <IntegratedSorting /> */}

                    <FilteringState
                        onFiltersChange={this.props.onFilterChange}
                        defaultFilters={[]} />

                    <Table />

                    <TableHeaderRow
                        showSortingControls
                        sortLabelComponent={ClientsSortLabel} />

                    <TableFilterRow cellComponent={ClientsFilterCell} />

                    <PagingPanel pageSizes={pageSizes} />
                    
                </Grid>
            </>
        )
    }
}