//#region Global Imports
import * as React from 'react';
import {
	Grid,
    Table,
    TableHeaderRow,
} from '@devexpress/dx-react-grid-material-ui';
import {
    DataTypeProvider,
} from '@devexpress/dx-react-grid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faCheckSquare,
    faSort,
	faSortUp,
	faSortDown,
} from '@fortawesome/free-solid-svg-icons';
import { faSquare } from '@fortawesome/free-regular-svg-icons';
import { makeStyles } from '@material-ui/styles';
import * as MaterialUI from '@material-ui/core';

//#endregion Global Imports

const cellSelectStyles = makeStyles({
	select: {
		backgroundColor: 'transparent',
	},
	disabled: {
		color: "#272E4C"
	}
});

const SortingIcon = ({ direction }: any) =>
	direction === 'asc' ? (
		<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
	) : (
		<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
	);

const SortLabel = ({ onSort, children, direction }: any) => {
	return (
		<MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort}>
			{children}
			{direction ? (
				<SortingIcon direction={direction} />
			) : (
				<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
			)}
		</MaterialUI.TableSortLabel>
	);
};

const Select = ({ items, ...restProps  }: any) => {
	const cellSelectClasses = cellSelectStyles();
	return (
		<MaterialUI.Select
			classes={cellSelectClasses}
			style={{ width: '100%', backgroundColor: 'transparent' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '——';
					}
				}
				return '——';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

const FrequencyItems = [
    { 
        label: 'Daily', 
        value: 'daily' 
    },
    { 
        label: 'Weekly', 
        value: 'weekly' 
    },
    { 
        label: 'Monthly', 
        value: 'monthly' 
    },
    { 
        label: 'Quarterly', 
        value: 'quarterly' 
    },
    { 
        label: 'Yearly', 
        value: 'yearly' 
    },
];

interface ReportorialTableProps {
	rows: any[];
	columns: any[];
	columnExtensions?: any[];
    formattedColumns?: any;
    updateReportorialData?: any;
}

const defaultProps: ReportorialTableProps = {
	rows: [],
	columns: [],
	columnExtensions: [],
	formattedColumns: {},
	updateReportorialData: () => {},
};

export const ReportorialTable: React.FC<ReportorialTableProps> = (
	props: ReportorialTableProps,
): JSX.Element => {
	const {
		rows,
		columns,
		columnExtensions,
        formattedColumns,
        updateReportorialData,
	} = props;
	
    const dataTypeProviders: any[] = [];
    
    const TextFormatter = ({ value }: any) => {
		return <MaterialUI.Typography style={{color: '#272E4C', textTransform: 'capitalize', fontSize: 14, fontWeight: 600}}>{value}</MaterialUI.Typography>;
	};

	const TextDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={TextFormatter} {...props} />
	);
    
	const FrequencyFormatter = ({ column, row, value }: any) => {
		const items = FrequencyItems;

		return <Select 
					onChange={(e) => {
                        updateReportorialData(row.report_type, 'frequency', e.target.value);
                    }}
					name={column['name']}
					items={items}
					id={'frequency'}
					value={value}
				/>;
	};

	const FrequencyDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={FrequencyFormatter} {...props} />
    );
    
	const IsAvailableFormatter = ({ column, value, row  }: any) => {
		return (
			<React.Fragment>
				<MaterialUI.Checkbox
					name={column['name']}
					icon={<FontAwesomeIcon icon={faSquare} />}
					checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
					onChange={e => {
						e.stopPropagation();
						updateReportorialData(row.report_type, 'available', !value);
					}}
					checked={value}
				/>
			</React.Fragment>
		);
	};

	const IsAvailableDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={IsAvailableFormatter} {...props} />
    );
    
	if (formattedColumns !== undefined) {
		if (formattedColumns['text'] !== undefined) {
			dataTypeProviders.push(<TextDataTypeProvider for={formattedColumns.text} />);
		}
		if (formattedColumns['frequency'] !== undefined) {
			dataTypeProviders.push(<FrequencyDataTypeProvider for={formattedColumns.frequency} />);
		}
		if (formattedColumns['isavailable'] !== undefined) {
			dataTypeProviders.push(<IsAvailableDataTypeProvider for={formattedColumns.isavailable} />);
		}
	}

	return (
		<div>
			<Grid rows={rows} columns={columns}>
				{dataTypeProviders}
                <Table columnExtensions={columnExtensions} />
                <TableHeaderRow 
                    showSortingControls={false}
                    sortLabelComponent={SortLabel} />
			</Grid>
		</div>
	);
};

ReportorialTable.defaultProps = defaultProps;
