    function updateClientStatus() {
        const { _id: clientId } = props.clientInfo;
        if (clientId) {
            setLoading(true);

            const newClientData = cloneDeep(props.clientInfo);
            newClientData.status = newClientStatus;

            API.patchClientStatus(clientId, newClientData)
            .then(response => {
                setLoading(false);
                if (response && response.ok) {
                    setMssgTitle('Success');
                    setMssgBody('Status successfully updated.');
                    setMssgAction('client_update_status_success');
                    setMssgOpen(true);
                } else {
                    console.log('update client api error', response);
                    setMssgTitle('Error');
                    setMssgBody('An error occurred while updating client status.');
                    setMssgAction('client_update_status_error');
                    setMssgOpen(true);
                }
            });
        }
        else{
            console.log('update client api error: no client id');
            setMssgTitle('Error');
            setMssgBody('An error occurred while updating client status.');
            setMssgAction('client_update_status_error');
            setMssgOpen(true);
        }
    }
    const postAuditlogs = (payload : any) => {
        console.log(clientDataInitial);
        let changes = {

        };
        let initialValue = {

        };
        for(let key in clientDataInitial) { 
            if(clientDataInitial[key] == payload[key]) {
                continue;
            }
            else {
                if(payload[key] !== undefined){
                    changes[key] = payload[key];
                    initialValue[key] = clientDataInitial[key];
                }
            }
        }
        let audit_logs_payload = {
            activity: "Update Client Profile",
            user: localStorage['first_name']+" "+localStorage['last_name'],
            client_id: props.match.params['client_id'],
            user_id: localStorage['user_id'],
            custom_metadata: {
                initial_values: initialValue,
                updated_values: changes
            }
        }
        console.log("audit_logs", JSON.stringify(audit_logs_payload));
        API.postAuditLogs(audit_logs_payload).then(()=>{
            console.log("inserted to audit logs");
        });
    }
