interface ClientContactProps {
  clientInfo: any;
}

interface ClientContactAddress { // Just for intellisense
	floor: number;
	unit: string;
	bldg_no: string;
	bldg_name: string;
	street: string;
	brgy: string;
	city: string;
	province?: string;
	region: string;
	zip_code: string;
	contact_numbers: { no: string }[];
	contact_persons: {
		name: string,
		contact_no: string,
		email_address: string,
		designation: string
	}[];
}

const ClientContact = (props: ClientContactProps) => {
  console.log(props.clientInfo);
  const classes = clientContactStyles();

    const renderAddressSection = (address: ClientContactAddress) => {
        const city = cityList.find(city => city.value === address.city);
        const region = regionList.find(region => region.value === address.region);
        return (
            <Card style={{ marginBottom: '20px' }}>
                <CardContent>
                    <Grid container>
                        <Grid item xs={12} className={classes.gridItem}>
                            <ClientInfoItem
                                name="BRANCH"
                                value={<b>{"Head Office"}</b>}
                                valueFontSize={18} />
                        </Grid>
                        <Grid item xs={12} className={classes.gridItem}>
                            <hr className={classes.hr} />
                        </Grid>
                        {/* Row #1 */}
                        <Grid item xs={12} sm={3} className={classes.gridItem}>
                            <ClientInfoItem
                                name="FLOOR / UNIT"
                                value={`${address.floor} / ${address.unit}`} />
                        </Grid>
                        <Grid item xs={12} sm={3} className={classes.gridItem}>
                            <ClientInfoItem name="BUILDING" value={address.bldg_name} />
                        </Grid>
                        <Grid item xs={12} sm={3} className={classes.gridItem}>
                            <ClientInfoItem name="STREET" value={address.street} />
                        </Grid>
                        <Grid item xs={12} sm={3} className={classes.gridItem}>
                            <ClientInfoItem name="BARANGAY" value={address.brgy} />
                        </Grid>
                        <GridHorizontalDivider height="30px" />
                        {/* Row #2 */}
                        {city ? (
                            <Grid item xs={12} sm={3} className={classes.gridItem}>
                                <ClientInfoItem
                                    name="CITY / TOWN"
                                    value={`${city.name} City`} />
                            </Grid>
                        ) : null}
                        {address.province ? (
                            <Grid item xs={12} sm={3} className={classes.gridItem}>
                                <ClientInfoItem name="PROVINCE" value={address.province} />
                            </Grid>
                        ) : null}
                        {region ? (
                            <Grid item xs={12} sm={3} className={classes.gridItem}>
                                <ClientInfoItem
                                    name="REGION / ZIP CODE"
                                    value={`${region.name} / ${address.zip_code}`} />
                            </Grid>
                        ) : null}
                        {address.contact_numbers ? (
                            <Grid item xs={12} sm={3} className={classes.gridItem}>
                                <ClientInfoItem
                                    name="TRUNKLINE / TEL. NO."
                                    value={
                                        address.contact_numbers.length > 0 ?
                                            address.contact_numbers[0].no
                                        : '---'
                                    } />
                            </Grid>
                        ) : null}
                        <GridHorizontalDivider height="30px" />
                        {/* Row #3 */}
            {address.contact_persons && address.contact_persons.length > 0 ? (
              <>
              <Typography className={classes.name}>CONTACT PERSON/S</Typography>
              <GridHorizontalDivider height="0" />
              <Grid item xs={12} sm={3} className={classes.gridItem}>
                <ClientInfoItem
                  name="NAME"
                  value={
                    address.contact_persons.map(person => {
                      return <div>{person.name}</div>
                    })
                  } />
              </Grid>
              <Grid item xs={12} sm={3} className={classes.gridItem}>
                <ClientInfoItem
                  name="POSITION"
                  value={
                    address.contact_persons.map(person => {
                      return <div>{person.designation}</div>
                    })
                  } />
              </Grid>
              <Grid item xs={12} sm={3} className={classes.gridItem}>
                <ClientInfoItem
                  name="CONTACT NO."
                  value={
                    address.contact_persons.map(person => {
                      return <div>{person.contact_no}</div>
                    })
                  } />
              </Grid>
              <Grid item xs={12} sm={3} className={classes.gridItem}>
                <ClientInfoItem
                  name="CONTACT EMAIL"
                  value={
                    address.contact_persons.map(person => {
                      return <div>{person.email_address}</div>
                    })
                  } />
              </Grid>
              </>
            ) : null}
            <GridHorizontalDivider height="30px" />
                    </Grid>
                </CardContent>
            </Card>
        );
    }

    return (
        <div>
            {props.clientInfo.main_office_address ? (
                renderAddressSection(props.clientInfo.main_office_address)
            ) : null}
            {props.clientInfo.branches.map(branch => {
                return renderAddressSection(branch)
            })}
        </div>
    );
};

