const mapStateToProps = (state: Store) => {
  return { 
      clientInfo: getClientInfo(state),
      proposalInfo: getProposalInfo(state)
    };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(ClientProfileActions.Map, dispatch),
  SetClientInfo: bindActionCreators(
    ClientProfileActions.SetClientInfo,
    dispatch,
  ),
  SetProposalInfo: bindActionCreators(
    ClientProfileActions.SetProposalInfo,
    dispatch,
  ),
});

export { mapStateToProps, mapDispatchToProps };
