import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Typography, Box } from '@material-ui/core/';
import { SICheckboxItem } from '../SICheckboxItem';
import { pull } from 'lodash';

interface EnrollmentProps {
    data: any;
    updateData: any;
}

const itemStyles = makeStyles({
    subheader: {
        fontSize: '1rem',
        color: '#272E4C',
        paddingBottom: 8,
    },
});

export const Enrollment: React.FC<EnrollmentProps> = (
    props: EnrollmentProps,
): JSX.Element => {

    const classes = itemStyles(props);

    let dependentEnrollmentMap = {
        hrc_uploading: false,
        hrc_encoding: false,
        mc_upload: false,
        mc_submission: false,
        mc_approval: false,
    };

    if(props.data && props.data['dependent_enrollment']){
        props.data['dependent_enrollment'].forEach(item => {
            if(item.enrollment === 'hr client uploading'){
                dependentEnrollmentMap['hrc_uploading'] = true;
            }
            else if(item.enrollment === 'hr client encoding'){
                dependentEnrollmentMap['hrc_encoding'] = true;
            }
            else if(item.enrollment === 'member client encoding'){
                dependentEnrollmentMap['mc_upload'] = item.options && item.options.indexOf('upload supporting') > -1;
                dependentEnrollmentMap['mc_submission'] = item.options && item.options.indexOf('submission of physical') > -1;
                dependentEnrollmentMap['mc_approval'] = item.options && item.options.indexOf('approval of hr') > -1;
            }
        });
    }

    const [dependentEnrollmentData, setDEData] = useState(dependentEnrollmentMap);

    const updateEnrollmentData = (key: string, value: any) => {
        let currData = props.data ? props.data : {};
        currData[key] = value;
        props.updateData('enrollment', currData);
    };

    const handleCheckboxChange = (key: string, code: string, newValue: boolean) => {
        if(newValue){
            if(props.data && props.data[key]){
                updateEnrollmentData(key, [...props.data[key], code]);
            }
            else{
                updateEnrollmentData(key, [code]);
            }
        }
        else{
            if(props.data && props.data[key]){
                updateEnrollmentData(key, [...pull(props.data[key], code)]);
            }
            else{
                updateEnrollmentData(key, []);
            }
        }
    };

    const isChecked = (key: string, code: string) => {
        return props.data && props.data[key] && props.data[key].indexOf(code) > -1;
    };

    const handleDEChange = (code: string, newValue: string) => {
        let currDEData = dependentEnrollmentData;
        currDEData[code] = newValue;
        setDEData(currDEData);
        let de_data: any[] = [];
        if(currDEData['hrc_uploading']){
            de_data.push({
                enrollment: 'hr client uploading'
            });
        }
        if(currDEData['hrc_encoding']){
            de_data.push({
                enrollment: 'hr client encoding'
            });
        }
        if(currDEData['mc_upload'] || currDEData['mc_submission'] || currDEData['mc_approval']){
            let options: string[] = [];
            if(currDEData['mc_upload']){
                options.push('upload supporting');
            }
            if(currDEData['mc_submission']){
                options.push('submission of physical');
            }
            if(currDEData['mc_approval']){
                options.push('approval of hr');
            } 
            de_data.push({
                enrollment: 'member client encoding',
                options: options
            });
        }

        updateEnrollmentData('dependent_enrollment', de_data);
    }
  
    return (
        <div>
            <Grid container>
                <Grid item xs={12}>
                    <Typography className={classes.subheader}>
                        Employee Enrollment
                    </Typography>
                    <SICheckboxItem 
                        label="HR Client uploading"
                        checked={isChecked('employee_enrollment', 'hr client uploading')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('employee_enrollment', 'hr client uploading', newValue);
                        }} />
                    <SICheckboxItem 
                        label="HR Client encoding"
                        checked={isChecked('employee_enrollment', 'hr client encoding')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('employee_enrollment', 'hr client encoding', newValue);
                        }} />
                </Grid>
                <Grid item xs={12} style={{paddingTop: 24}}>
                    <Typography className={classes.subheader}>
                        Dependent Enrollment
                    </Typography>
                    <SICheckboxItem 
                        label="HR Client uploading"
                        checked={dependentEnrollmentData['hrc_uploading']}
                        handleChange={(newValue) => {
                            handleDEChange('hrc_uploading', newValue);
                        }} />
                    <SICheckboxItem 
                        label="HR Client encoding"
                        checked={dependentEnrollmentData['hrc_encoding']}
                        handleChange={(newValue) => {
                            handleDEChange('hrc_encoding', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Member Client encoding"
                        disabled={true}
                        checked={dependentEnrollmentData['mc_upload'] || dependentEnrollmentData['mc_submission'] || dependentEnrollmentData['mc_approval']}
                        handleChange={() => {}} />
                    <Box style={{marginLeft: 24}}>
                        <SICheckboxItem 
                            label="Upload supporting documents required"
                            checked={dependentEnrollmentData['mc_upload']}
                            handleChange={(newValue) => {
                                handleDEChange('mc_upload', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Submission of physical supporting documents required"
                            checked={dependentEnrollmentData['mc_submission']}
                            handleChange={(newValue) => {
                                handleDEChange('mc_submission', newValue);
                            }} />
                        <SICheckboxItem 
                            label="Approval of HR through HR Client Portal required"
                            checked={dependentEnrollmentData['mc_approval']}
                            handleChange={(newValue) => {
                                handleDEChange('mc_approval', newValue);
                            }} />
                    </Box>
                </Grid>
                <Grid item xs={12} style={{paddingTop: 24}}>
                    <Typography className={classes.subheader}>
                        Enrollment done through
                    </Typography>
                    <SICheckboxItem 
                        label="Email"
                        checked={isChecked('enrollment_done_through', 'email')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('enrollment_done_through', 'email', newValue);
                        }} />
                    <SICheckboxItem 
                        label="Physical documents"
                        checked={isChecked('enrollment_done_through', 'physical documents')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('enrollment_done_through', 'physical documents', newValue);
                        }} />
                </Grid>
            </Grid>
        </div>
    );
};