import React from 'react';
import {
	BasicSelect,
} from 'Components/UI/';
import { clientInformationStyles } from '../../ClientProfilePage'
import { MaterialUiPickersDate } from '@material-ui/pickers';
import { regionList } from 'Pages/CreateProposal/Details';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	Grid,
	FormControlLabel,
	Typography,
	RadioGroup,
	Radio,
	//TableRow,
	//TableCell,
	InputLabel,
	Checkbox,
} from '@material-ui/core';
import { faCheckSquare,
} from '@fortawesome/free-solid-svg-icons';
import { faSquare } from '@fortawesome/free-regular-svg-icons';
import { KeyboardDatePicker } from '@material-ui/pickers';
import {
	GridHorizontalDivider
} from 'Components/UI';

interface ClientInformation {
	proposalInfo?:any;
	setEditInfo?:(e) => void;
	setData:any;
	setSideNav?:(e)=>void;
}

export const EditProposal = (props:ClientInformation) => {
	const classes = clientInformationStyles();
	const handleCheckboxOnChange = (name: string, key: string) => (event: any) => {
		//const data = { ...props.data, [name]: event.target.checked };
		const data = Object.assign({}, props.proposalInfo);
		if(event.target.checked){
			if(data && data[key] && !data[key].includes(name)){
				data[key].push(name)
			}
		}else{
			if(data && data[key] && data[key].includes(name)){
				const index = data[key].indexOf(name);
				if(index !== -1){
					data[key].splice(index,1);
				}
			}
		}
		if (props.setData) {
			props.setData(data);
		}
	};
	const handleDatePickerOnChange = (name: string, date: MaterialUiPickersDate | null) => {
		const data = { ...props.proposalInfo, [name]: date ? date.toDate() : null };
		if (props.setData) {
			props.setData(data);
		}
	};
	const handleTextFieldOnChange = (name: string) => (event: any) => {
		if(event.target.value.trim() === '')
			{
				event.target.value = event.target.value.trim();
			}
		const data = { ...props.proposalInfo, [name]: event.target.value };
		if (props.setData) {
			props.setData(data);
		}
	};
	return (
		<Grid container>
			<Grid item xs={12} className={classes.editContainer}>
					<InputLabel htmlFor="effectivityDate" className={classes.inputLabel}>
						Coverage Period
					</InputLabel>
					<div style={{ display: 'flex', alignItems: 'center' }}>
						<KeyboardDatePicker
							id="effectivityDate"
							variant="dialog"
							format="MM/DD/YYYY"
							inputVariant="outlined"
							className={classes.keyboardDatePicker}
							value={props.proposalInfo.coverage_start_date}
							invalidDateMessage=""
							onChange={date => handleDatePickerOnChange('effectivityDate', date)}
						/>
						<Typography display="inline">
							&nbsp;&nbsp;&nbsp;&mdash;&nbsp;&nbsp;&nbsp;
						</Typography>
						<KeyboardDatePicker
							id="expiryDate"
							variant="dialog"
							format="MM/DD/YYYY"
							inputVariant="outlined"
							className={classes.keyboardDatePicker}
							value={props.proposalInfo.coverage_end_date}
							invalidDateMessage=""
							onChange={date => handleDatePickerOnChange('expiryDate', date)}
						/>
					</div>
			</Grid>
			<GridHorizontalDivider height="30px" />
			<Grid item xs={12} className={classes.editContainer}>
				<Typography className={classes.AddPropHeader}>
					Additional Proposal Details
				</Typography>
				<Typography className={classes.AddPropInst}>
					Choose details that will be included in the generated proposal
				</Typography>
			</Grid>
			<GridHorizontalDivider height="20px" />
			<Grid item xs={6} className={classes.editContainer}>
				<Checkbox
					name='Company Name'
					icon={<FontAwesomeIcon icon={faSquare} />}
					checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
					onClick={handleCheckboxOnChange('Company Name', 'additional_details')}
					checked={props.proposalInfo && props.proposalInfo.additional_details 
						&& props.proposalInfo.additional_details.includes("Company Name") ? 
						true:false}
				/>
				<Typography display='inline' className={classes.AddPropHeader}>
					Company Name
				</Typography>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<Checkbox
					name='Contact Person'
					icon={<FontAwesomeIcon icon={faSquare} />}
					checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
					onClick={handleCheckboxOnChange('Contact Person', 'additional_details')}
					checked={props.proposalInfo && props.proposalInfo.additional_details 
						&& props.proposalInfo.additional_details.includes("Contact Person") ? 
						true:false}
				/>
				<Typography display='inline' className={classes.AddPropHeader}>
					Contact Person
				</Typography>
			</Grid>
			<Grid item xs={6} className={classes.ProposalRadio}>
				<RadioGroup
						value={props.proposalInfo.CompanyNameSelected}
						onChange={handleTextFieldOnChange('CompanyNameSelected')}
					>
						
						<FormControlLabel
							value="Registered Business Name"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Registered Business Name"
						/>
						<FormControlLabel
							value="Trade Name"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Trade Name"
						/>
				</RadioGroup>
			</Grid>
			
			<Grid item xs={6} className={classes.ProposalRadio}>
				<RadioGroup
						value={props.proposalInfo.ContactPersonSelected}
						onChange={handleTextFieldOnChange('ContactPersonSelected')}
					>
						
						<FormControlLabel
							value="Human Resources"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Human Resources"
						/>
						<FormControlLabel
							value="Finance"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Finance"
						/>
						<FormControlLabel
							value="Signatory"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Signatory"
						/>
				</RadioGroup>
			</Grid>
			<Grid item xs={12} className={classes.editContainer}>
				<Checkbox
					name='Address'
					icon={<FontAwesomeIcon icon={faSquare} />}
					checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
					onClick={handleCheckboxOnChange('Address', 'additional_details')}
					checked={props.proposalInfo && props.proposalInfo.additional_details 
						&& props.proposalInfo.additional_details.includes("Address") ? 
						true:false}
				/>
				<Typography display='inline' className={classes.AddPropHeader}>
					Address
				</Typography>
			</Grid>
			<Grid item xs={6} className={classes.ProposalRadio}>
				<RadioGroup
						value={props.proposalInfo.AddressSelected}
						onChange={handleTextFieldOnChange('AddressSelected')}
					>
						
						<FormControlLabel
							value="Head Office"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Head Office"
						/>
						<FormControlLabel
							value="branch"
							control={
								<Radio
									color="primary"
									classes={{
										colorPrimary: classes.radio,
									}}
								/>
							}
							label="Branch"
						/>
				</RadioGroup>
				<Grid item xs={9} className={classes.ProposalRadio}>
					<BasicSelect
							id="region"
							menuItems={regionList}
							value={props.proposalInfo.SelectedBranch}
							onChange={handleTextFieldOnChange('SelectedBranch')}
							width='100%'
					/>
				</Grid>
					
			</Grid>
		</Grid>
		
	);
};
