const getColumnExtensions = () => {
  const columns: any[] = [];

  columnNames.map(column => {
    let width = 200;

    switch (column.name) {
      case 'urgency':
      case 'status':
      case 'app_uid':
        width = 100;
        break;
      case 'type':
      case 'dueDate':
        width = 120;
        break;
      case 'specificType':
      case 'company':
        width = 250;
        break;
      default:
        width = 200;
    }

    columns.push({
      columnName: column.name,
      width: width,
      wordWrapEnabled: true,
    });
  });

  return columns;
};

