import { clientInformationStyles } from '../ClientProfilePage';
import React,{ useState,useEffect }  from 'react';
import {
    Grid,
    TextField,
    TableRow,
    TableCell,
    Paper,
} from '@material-ui/core';
import { LoadingIndicator } from 'Components/UI/';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { PositionProperty } from 'csstype';
import { IconButton } from '../Components/IconButton';
import { InputField } from '../Components/InputField'; 
import { HistoryTable } from '../Components/HistoryTable';
import {
	GridHorizontalDivider,
} from 'Components/UI/';
import {
    faSearch,
    faSort,
  } from '@fortawesome/free-solid-svg-icons';
import Link from '@material-ui/core/Link';
import { API } from '../../API';

interface ClientInformation { 
	clientId:string;
}
//let API_URL = process.env.REACT_APP_BASE_URL;
let topnav = 'contracts'; 

export const History = (props:ClientInformation) => {
    const [loading, setLoading] = useState(false);
    //const [rows, setRows] = useState<any[]>([]);
    const [filters, setFilters] = useState<any[]>([]);
    //const [documentdata, setDocumentData] = useState<any[]>([]);
    const [selectedColumns, setSelectedColumns] = useState<any[]>([]);
    const [selectedRows, setSelectedRows] = useState<any[]>([]);
    const [DocuTable,setDocuTable] = useState<any>()
    const [renderTable,setRenderTable] = useState(true)
    //const [topnav, setTopNav] = useState('contracts');
    const [largeSearch , setLargeSearch] = useState('');
    const handleLoadClientsFromAPI = () => {
        setLoading(true);
        //if(topnav.trim() ==='')
        //{topnav='contracts'}
	    API.getHistory(props.clientId,topnav).then(response => {
            console.log(response);
	        if(response && response.count>0 && response.error === undefined) {
                let tmp:any[]=[]
                console.log('check');
                //console.log(cont);
                (topnav==='contracts' ? 
                
                    response.data.map(col =>{
                      let effdate = 
                      col.custom_metadata.coverage_start_date &&
                      col.custom_metadata.coverage_end_date ?
                      (new Date(
                        col.custom_metadata.coverage_start_date)).toLocaleDateString('en-US',
                        {
                          day:'numeric',
                          year:'numeric',
                          month:'short'
                        }) + ' - ' + (new Date(
                          col.custom_metadata.coverage_end_date)).toLocaleDateString('en-US',
                          {
                            day:'numeric',
                            year:'numeric',
                            month:'short'
                          }) : '-'
                      tmp.push({
                        contract : col.name ? col.name : '-',
                        contract_no : col.contract_no ? col.contract_no : '-',
                        effectivity_date : effdate,
                        contract_type : col.handler ? col.handler : '-', 
                        version : col.version ? col.version : '-',
                        modified_date : col.custom_metadata.date_updated ? 
                            (new Date(col.custom_metadata.date_updated).toLocaleDateString('en-US',
                            {
                              day:'numeric',
                              year:'numeric',
                              month:'short'
                            }))
                            : '-',
                        status : col.status ? col.status : '-' 
                      })
                    })
                   : 
                    response.data.map(col =>{
                      let effdate = 
                      col.custom_metadata.coverage_start_date &&
                      col.custom_metadata.coverage_end_date ?
                      (new Date(
                        col.custom_metadata.coverage_start_date)).toLocaleDateString('en-US',
                        {
                          day:'numeric',
                          year:'numeric',
                          month:'short'
                        }) + ' - ' + (new Date(
                          col.custom_metadata.coverage_end_date)).toLocaleDateString('en-US',
                          {
                            day:'numeric',
                            year:'numeric',
                            month:'short'
                          }) : '-'
                      tmp.push({
                        proposal_number : col.proposal_no ? col.proposal_no : '-',
                        effectivity_date : effdate,
                        bdobroker : col.broker ? col.broker : '-',
                        date_created : col.custom_metadata.date_created ? 
                          (new Date(col.custom_metadata.date_created).toLocaleDateString('en-US',
                          {
                            day:'numeric',
                            year:'numeric',
                            month:'short'
                          }))
                          : '-', 
                        date_modified : col.custom_metadata.date_updated ? 
                          (new Date(col.custom_metadata.date_updated).toLocaleDateString('en-US',
                          {
                            day:'numeric',
                            year:'numeric',
                            month:'short'
                          }))
                          : '-',
                        version : col.version ? col.version : '', 
                        status : col.status ? col.status : '' 
                      })
                    })
                  )
                  console.log('tmp')
                console.log(tmp);
                setSelectedRows(tmp);
                setRenderTable(true)
                setLoading(false);
	        }else{
        setLoading(false);
        setSelectedRows([]);
        setRenderTable(true)
				console.log('getbenefitlist api error: ', response);
	        }
	    });
    }
     
      
    const rowcustomstyle = {
      left: 0,
      zIndex: 125,
      position: 'relative' as PositionProperty,
      minWidth: '20px',
      maxWidth: '300px',
      paddingRight : '0px',
      paddingLeft : '10px',
    };



    

    const contractcolumn: any = [
		{ 
			name: 'contract', 
			title: 'Contract'
		},
		{
			name: 'contract_no', 
			title: 'Contract No.'
		},
		{
			name: 'effectivity_date', 
			title: 'Contract Effectivity Date'
		},
		{
			name: 'contract_type', 
			title: 'Contract Type', 
		},
		{
			name: 'version', 
			title: 'Version',
        },
        {
            name: 'modified_date', 
            title: 'Modified Date',
        },
        {
			name: 'status', 
			title: 'Status', 
		},
    ];
    const proposalcolumn: any = [
      { 
        name: 'proposal_number', 
        title: 'Proposal No.'
      },
      {
        name: 'effectivity_date', 
        title: 'Effectivity Date'
      },
      {
        name: 'bdobroker', 
        title: 'BDO/Broker'
      },
      {
        name: 'date_created', 
        title: 'Date Created', 
      },
      {
        name: 'date_modified', 
        title: 'Date Modified',
      },
      {
        name: 'version', 
        title: 'Version',
      },
      {
        name: 'status', 
        title: 'Status',
      },
    ];

    useEffect(() => { 
      //topnav = 'signed_contracts'
      setSelectedColumns(contractcolumn);
      //setSelectedRows(contractsrows);
      handleLoadClientsFromAPI();
    }, []);

    const classes = clientInformationStyles();

    const sortTickets = (attrName: string) => {
      let sortbool:boolean = true;
      (selectedRows.length>1 ?
      sortbool = selectedRows[0][attrName].toString().toLowerCase()>selectedRows[1][attrName].toString().toLowerCase()
      : sortbool = true);
      selectedRows.sort((a,b) => 
          (a[attrName] && b[attrName] && 
            a[attrName].toString().toLowerCase()>b[attrName].toString().toLowerCase()
            && sortbool) ?
            (1) : (-1)
      );
      setSelectedRows(selectedRows);
      console.log(selectedRows)
    }
    const renderCustomTableHeader = (columns: any, fixedColumns: any) => {
        let dockedColumnsCounter = 0;
            return (
              <>
              {columns.map((column, index) => {
                const matchedColumn = fixedColumns.filter(col => col === column.name)[0];
                let nextColumnIsFixed = true;
                if (matchedColumn && columns[index + 1]) {
                  nextColumnIsFixed = fixedColumns.filter(col => col ===  columns[index + 1].name)[0] ? true : false;
                }
                const headstyle = {
                  left: matchedColumn ? dockedColumnsCounter * 125 : 0,
                  zIndex: matchedColumn ? 125 : 0,
                  minWidth: '20px',
                  maxWidth: '300px',
                  paddingRight : '0px',
                  paddingLeft : '10px',
                  borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined
                };
                if (matchedColumn) {
                  dockedColumnsCounter++;
                }
                return (
                  <TableCell style={headstyle}>
                    <span className='header-2'>{column.title}</span>
                    <div style={{float: 'right'}}>
                        <IconButton
                            icon={faSort}
                            onClick={() => {
                                sortTickets(column.name);
                                setRenderTable(true);
                                setLoading(false);
                            }} />
                    </div>
                    <InputField
                        style={column.name==='effectivity_date'?{width:'180px'}:{}}
                        value={filters[column.name]}
                        onChange={(value: string) => {
                            filters[column.name] = value;
                            setFilters(filters);
                            setRenderTable(true);
                            // this.setState(prevState => ({...prevState, [attrName]: value}));
                        }} />
                  </TableCell>
                );
              })}
              </>
            );
    }
    const renderCustomTableRow = (row: any, index: number, columns: any) => {
            console.log(row);
            console.log('column')
            console.log(columns)
            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

            /*let returnrow = row.length>0 ?
            row.map((rows:any)=>
              
            ) : <TableRow></TableRow>*/

            return (<TableRow
            style={{backgroundColor: backgroundColor}}
            onClick={() => {
              console.log('href');
            }}>
            {columns.map((column) => {
              return (
                <TableCell style={rowcustomstyle}>
                  {row[column.name]}
                </TableCell> 
              );
            })}
          </TableRow>);
    }
    const getFilteredTickets = (columns: any) => {
        let filteredTickets = selectedRows;
        console.log('filtered')
        console.log(selectedRows)
        if(largeSearch!=='')
        {   
            if(topnav === 'contracts')
            {
              filteredTickets = filteredTickets
              .filter(ticket => 
                 (ticket['contract_no'] && 
                    ticket['contract_no'].toString().toLowerCase().includes(largeSearch.toLowerCase())
                  ) || (ticket['effectivity_date'] && 
                  ticket['effectivity_date'].toString().toLowerCase().includes(largeSearch.toLowerCase())
                )
                
              )  
            } else {
              filteredTickets = filteredTickets
              .filter(ticket => 
                 (ticket['proposal_number'] && 
                    ticket['proposal_number'].toString().toLowerCase().includes(largeSearch.toLowerCase())
                  ) || (ticket['effectivity_date'] && 
                  ticket['effectivity_date'].toString().toLowerCase().includes(largeSearch.toLowerCase())
                )
                
              ) 
            }
        }
        columns.map(column => {
          filteredTickets = filteredTickets
            .filter(ticket => 
                filters[column.name] ? (
                    filters[column.name] === '' ||
                (ticket[column.name] && 
                  ticket[column.name].toString().toLowerCase().includes(filters[column.name].toLowerCase())
                )
              ) : true
            ) 
         });
    
        return filteredTickets;
      }
      let parentWidth = 900;
      const parentCont = document.getElementById('TableCont');
      if(parentCont!==null)
      {
        parentWidth = parentCont.offsetWidth; 
      } 
      const handleAssignTable =()=>{
       return(
        <HistoryTable
          className='ticket-list-table'
          canOverflow
          ContWidth={parentWidth}
          rows={getFilteredTickets(selectedColumns)}
          stickyHeader={true}
          renderHeader={() => renderCustomTableHeader(selectedColumns, selectedColumns)}
          renderRow={(row: any, index: number) => renderCustomTableRow(row, index, selectedColumns)}
          rowsPerPageOptions={[5, 10, 15]}
      />
       )
      }
      if(renderTable){
        setDocuTable(handleAssignTable());
        setRenderTable(false);
      }
  return (
    <div>
        { loading ? 
				(<LoadingIndicator/>) : null

			}
      <Grid container>
        <GridHorizontalDivider height="30px" />
        <Grid item xs={12} className={classes.gridItem}>
          <Link
            className={
                topnav === 'contracts'
                ? classes.HistoryTopNavSelected
                : classes.HistoryTopNavNotSelected 
            }
            onClick={() => {
                topnav = 'contracts';
                //setTopNav('contracts');
                setSelectedColumns(contractcolumn);
                //setSelectedRows(contractsrows);
                handleLoadClientsFromAPI()
            }}
          >
            Contracts
          </Link>
          <Link
            className={
                topnav === 'proposals'
                ? classes.HistoryTopNavSelected
                : classes.HistoryTopNavNotSelected
            }
            onClick={() => {
                topnav = 'proposals';
                //setTopNav('proposals');
                setSelectedColumns(proposalcolumn);
                //setSelectedRows(proposalsrows)
                handleLoadClientsFromAPI()
            }}
          >
            Proposals
          </Link>
        </Grid>
        <GridHorizontalDivider height="30px" />
        <Grid item sm={12}>
          <TextField
            id="history_search"
            placeholder="Search year or proposal number"
            className={classes.searchField}
            margin="dense"
            variant="outlined"
            fullWidth
            inputProps={{ 'aria-label': 'bare' }}
            value={largeSearch}
            onChange={(event:any)=>{
                setLargeSearch(event.target.value.trim())
                setRenderTable(true)}}
            InputProps={{
              startAdornment: (
                <FontAwesomeIcon
                  icon={faSearch}
                  className={classes.searchInputAdornment}
                />
              ),
            }}
          />
        </Grid>
        <GridHorizontalDivider height="30px" />
        <Grid item xs={12} id='TableCont'>
          <Paper style={{overflowX:'auto',maxWidth:'100%'}}>
              {DocuTable}
          </Paper>
        </Grid>
        
      </Grid>
      
    </div>
  );
}